<template><section>
<h1>基础树</h1>
<h2>基础使用</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-tree node-key=\&quot;id\&quot; :data=\&quot;treeData\&quot;&gt;&lt;/base-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      treeData: [\n        {\n          id: 1,\n          label: '一级 1',\n          hasChildren: true,\n          children: [{\n            id: 4,\n            label: '二级 1-1',\n            hasChildren: true,\n            children: [{\n              id: 9,\n              label: '三级 1-1-1',\n              hasChildren: false\n            }, {\n              id: 10,\n              label: '三级 1-1-2',\n              hasChildren: false\n            }]\n          }]\n        }, {\n          id: 2,\n          label: '一级 2',\n          hasChildren: true,\n          children: [{\n            id: 5,\n            label: '二级 2-1',\n            hasChildren: false\n          }, {\n            id: 6,\n            label: '二级 2-2',\n            hasChildren: false\n          }]\n      }]\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-tree node-key="id" :data="treeData"></base-tree>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-tree</span>
    <span class="hljs-attr">node-key</span>=<span class="hljs-string">"id"</span>
    <span class="hljs-attr">:data</span>=<span class="hljs-string">"treeData"</span>
  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">base-tree</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">treeData</span>: [
        {
          <span class="hljs-attr">id</span>: <span class="hljs-number">1</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'一级 1'</span>,
          <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">children</span>: [{
            <span class="hljs-attr">id</span>: <span class="hljs-number">4</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'二级 1-1'</span>,
            <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
            <span class="hljs-attr">children</span>: [{
              <span class="hljs-attr">id</span>: <span class="hljs-number">9</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">'三级 1-1-1'</span>,
              <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
            }, {
              <span class="hljs-attr">id</span>: <span class="hljs-number">10</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">'三级 1-1-2'</span>,
              <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
            }]
          }]
        }, {
          <span class="hljs-attr">id</span>: <span class="hljs-number">2</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'一级 2'</span>,
          <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">children</span>: [{
            <span class="hljs-attr">id</span>: <span class="hljs-number">5</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'二级 2-1'</span>,
            <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
          }, {
            <span class="hljs-attr">id</span>: <span class="hljs-number">6</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'二级 2-2'</span>,
            <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
          }]
      }]
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>可选择</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-tree :data=\&quot;treeData\&quot; show-checkbox&gt;&lt;/base-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      treeData: [\n        {\n          id: 1,\n          label: '一级 1',\n          hasChildren: true,\n          children: [{\n            id: 4,\n            label: '二级 1-1',\n            hasChildren: true,\n            children: [{\n              id: 9,\n              label: '三级 1-1-1',\n              hasChildren: false\n            }, {\n              id: 10,\n              label: '三级 1-1-2',\n              hasChildren: false\n            }]\n          }]\n        }, {\n          id: 2,\n          label: '一级 2',\n          hasChildren: true,\n          children: [{\n            id: 5,\n            label: '二级 2-1',\n            hasChildren: false\n          }, {\n            id: 6,\n            label: '二级 2-2',\n            hasChildren: false\n          }]\n      }]\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-tree :data="treeData" show-checkbox=""></base-tree>
</template>

</div>
                            <p><code>单击</code>：选择当前节点     <code>CTRL+单击</code>：选择当前节点和直接下级节点     <code>SHIFT+单击</code>：选择当前节点和全部下级节点</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-tree</span>
    <span class="hljs-attr">:data</span>=<span class="hljs-string">"treeData"</span>
    <span class="hljs-attr">show-checkbox</span>
