<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">treeData</span>: [
        {
          <span class="hljs-attr">id</span>: <span class="hljs-number">1</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'一级 1'</span>,
          <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">children</span>: [{
            <span class="hljs-attr">id</span>: <span class="hljs-number">4</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'二级 1-1'</span>,
            <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
            <span class="hljs-attr">children</span>: [{
              <span class="hljs-attr">id</span>: <span class="hljs-number">9</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">'三级 1-1-1'</span>,
              <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
            }, {
              <span class="hljs-attr">id</span>: <span class="hljs-number">10</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">'三级 1-1-2'</span>,
              <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
            }]
          }]
        }, {
          <span class="hljs-attr">id</span>: <span class="hljs-number">2</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'一级 2'</span>,
          <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">children</span>: [{
            <span class="hljs-attr">id</span>: <span class="hljs-number">5</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'二级 2-1'</span>,
            <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
          }, {
            <span class="hljs-attr">id</span>: <span class="hljs-number">6</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'二级 2-2'</span>,
            <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
          }]
      }]
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>节点过滤</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-tree ref=\&quot;filterTree\&quot; :data=\&quot;treeData\&quot; show-searchbox :filter-node-method=\&quot;filterNode\&quot; @on-search-change=\&quot;handleSearchChanged\&quot;&gt;&lt;/base-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      treeData: [\n        {\n          id: 1,\n          label: '一级 1',\n          hasChildren: true,\n          children: [{\n            id: 4,\n            label: '二级 1-1',\n            hasChildren: true,\n            children: [{\n              id: 9,\n              label: '三级 1-1-1',\n              hasChildren: false\n            }, {\n              id: 10,\n              label: '三级 1-1-2',\n              hasChildren: false\n            }]\n          }]\n        }, {\n          id: 2,\n          label: '一级 2',\n          hasChildren: true,\n          children: [{\n            id: 5,\n            label: '二级 2-1',\n            hasChildren: false\n          }, {\n            id: 6,\n            label: '二级 2-2',\n            hasChildren: false\n          }]\n      }]\n    }\n  },\n  methods: {\n    filterNode(value, data) {\n      if (!value) return true\n      return data.label.indexOf(value) !== -1\n    },\n    handleSearchChanged (text) {\n      this.$refs.filterTree.filter(text)\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-tree ref="filterTree" :data="treeData" show-searchbox="" :filter-node-method="filterNode" @on-search-change="handleSearchChanged"></base-tree>
</template>

</div>
                            <p>在需要对节点进行过滤时，调用 <code>Tree</code> 实例的<code>filter</code>方法，参数为关键字。需要注意的是，此时需要设置<code>filter-node-method</code>，值为过滤函数。</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-tree</span>
    <span class="hljs-attr">ref</span>=<span class="hljs-string">"filterTree"</span>
    <span class="hljs-attr">:data</span>=<span class="hljs-string">"treeData"</span>
    <span class="hljs-attr">show-searchbox</span>
    <span class="hljs-attr">:filter-node-method</span>=<span class="hljs-string">"filterNode"</span>
    <span class="hljs-attr">@on-search-change</span>=<span class="hljs-string">"handleSearchChanged"</span>
  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">base-tree</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">treeData</span>: [
        {
          <span class="hljs-attr">id</span>: <span class="hljs-number">1</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'一级 1'</span>,
          <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">children</span>: [{
            <span class="hljs-attr">id</span>: <span class="hljs-number">4</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'二级 1-1'</span>,
            <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
            <span class="hljs-attr">children</span>: [{
              <span class="hljs-attr">id</span>: <span class="hljs-number">9</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">'三级 1-1-1'</span>,
              <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
            }, {
              <span class="hljs-attr">id</span>: <span class="hljs-number">10</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">'三级 1-1-2'</span>,
              <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
            }]
          }]
        }, {
          <span class="hljs-attr">id</span>: <span class="hljs-number">2</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'一级 2'</span>,
          <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">children</span>: [{
            <span class="hljs-attr">id</span>: <span class="hljs-number">5</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'二级 2-1'</span>,
            <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
          }, {
            <span class="hljs-attr">id</span>: <span class="hljs-number">6</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'二级 2-2'</span>,
            <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">false</span>
          }]
      }]
    }
  },
  <span class="hljs-attr">methods</span>: {
    filterNode(value, data) {
      <span class="hljs-keyword">if</span> (!value) <span class="hljs-keyword">return</span> <span class="hljs-literal">true</span>
      <span class="hljs-keyword">return</span> data.label.indexOf(value) !== <span class="hljs-number">-1</span>
    },
    handleSearchChanged (text) {
      <span class="hljs-keyword">this</span>.$refs.filterTree.filter(text)
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>懒加载</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-tree :load=\&quot;loadNode\&quot; lazy&gt;&lt;/base-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      nodeId: 1\n    }\n  },\n  methods: {\n    loadNode(node, resolve) {\n      if (node.level === 0) {\n        // data为异步请求得到的数据,此处为模拟\n        const data = this.treeDataList.filter(item =&gt; item.parent === null)\n        return resolve(data)\n      }\n\n      if (node.level &gt; 0) {\n        // data为异步请求得到的数据,此处为模拟\n        const data = this.treeDataList.filter(item =&gt; item.parent === node.data.id)\n        // 提供第二个参数，能更好的高亮展开选中节点\n        resolve(data, this.currentNodeData.id)\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-tree :load="loadNode" lazy=""></base-tree>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-tree</span>
    <span class="hljs-attr">:load</span>=<span class="hljs-string">"loadNode"</span>
    <span class="hljs-attr">lazy</span>
  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">base-tree</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">nodeId</span>: <span class="hljs-number">1</span>
    }
  },
  <span class="hljs-attr">methods</span>: {
    loadNode(node, resolve) {
      <span class="hljs-keyword">if</span> (node.level === <span class="hljs-number">0</span>) {
        <span class="hljs-comment">// data为异步请求得到的数据,此处为模拟</span>
        <span class="hljs-keyword">const</span> data = <span class="hljs-keyword">this</span>.treeDataList.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> item.parent === <span class="hljs-literal">null</span>)
        <span class="hljs-keyword">return</span> resolve(data)
      }

      <span class="hljs-keyword">if</span> (node.level &gt; <span class="hljs-number">0</span>) {
        <span class="hljs-comment">// data为异步请求得到的数据,此处为模拟</span>
        <span class="hljs-keyword">const</span> data = <span class="hljs-keyword">this</span>.treeDataList.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> item.parent === node.data.id)
        <span class="hljs-comment">// 提供第二个参数，能更好的高亮展开选中节点</span>
        resolve(data, <span class="hljs-keyword">this</span>.currentNodeData.id)
      }
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>异步搜索和节点定位</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-tree node-key=\&quot;id\&quot; show-searchbox=\&quot;\&quot; :load=\&quot;loadNode2\&quot; lazy @on-search-change=\&quot;handleSearchChanged\&quot; @on-search-selected=\&quot;handleSearchSelected\&quot;&gt;&lt;/base-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      currentNodeData: {}\n    }\n  },\n  methods: {\n    handleSearchChanged (text, resolve) {\n      const data = this.treeDataList.filter(item =&gt; item.label.includes(text))\n      resolve(data)\n    },\n    handleSearchSelected (nodeData, resolve) {\n      this.currentNodeData = nodeData\n    },\n    loadNode2(node, resolve) {\n      if (node.level === 0) {\n        // data为异步请求得到的数据,此处为模拟\n        const data = this.treeDataList.filter(item =&gt; item.parent === null)\n        return resolve(data)\n      }\n\n      if (node.level &gt; 0) {\n        // data为异步请求得到的数据,此处为模拟\n        const data = this.treeDataList.filter(item =&gt; item.parent === node.data.id)\n        // 提供第二个参数，能更好的高亮展开选中节点\n        resolve(data, this.currentNodeData.id)\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-tree node-key="id" show-searchbox="" :load="loadNode2" lazy="" @on-search-change="handleSearchChanged" @on-search-selected="handleSearchSelected"></base-tree>
</template>

</div>
                            <p>在节点定位时，由于节点时异步加载的，在设置当前节点时有可能该节点还未加载完毕，为了更好的高亮展示选中节点，我们给load函数提供了第二个参数，只需要在点击搜索项时记下当前节点，传递给load函数，即可实现精准定位。</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-tree</span>
    <span class="hljs-attr">node-key</span>=<span class="hljs-string">"id"</span>
    <span class="hljs-attr">show-searchbox</span>
    <span class="hljs-attr">:load</span>=<span class="hljs-string">"loadNode2"</span>
    <span class="hljs-attr">lazy</span>
    <span class="hljs-attr">@on-search-change</span>=<span class="hljs-string">"handleSearchChanged"</span>
    <span class="hljs-attr">@on-search-selected</span>=<span class="hljs-string">"handleSearchSelected"</span>
  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">base-tree</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">currentNodeData</span>: {}
    }
  },
  <span class="hljs-attr">methods</span>: {
    handleSearchChanged (text, resolve) {
      <span class="hljs-keyword">const</span> data = <span class="hljs-keyword">this</span>.treeDataList.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> item.label.includes(text))
      resolve(data)
    },
    handleSearchSelected (nodeData, resolve) {
      <span class="hljs-keyword">this</span>.currentNodeData = nodeData
    },
    loadNode2(node, resolve) {
      <span class="hljs-keyword">if</span> (node.level === <span class="hljs-number">0</span>) {
        <span class="hljs-comment">// data为异步请求得到的数据,此处为模拟</span>
        <span class="hljs-keyword">const</span> data = <span class="hljs-keyword">this</span>.treeDataList.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> item.parent === <span class="hljs-literal">null</span>)
        <span class="hljs-keyword">return</span> resolve(data)
      }

      <span class="hljs-keyword">if</span> (node.level &gt; <span class="hljs-number">0</span>) {
        <span class="hljs-comment">// data为异步请求得到的数据,此处为模拟</span>
        <span class="hljs-keyword">const</span> data = <span class="hljs-keyword">this</span>.treeDataList.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> item.parent === node.data.id)
        <span class="hljs-comment">// 提供第二个参数，能更好的高亮展开选中节点</span>
        resolve(data, <span class="hljs-keyword">this</span>.currentNodeData.id)
      }
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>API</h2>
<h3>Toolbar Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>展示数据</td>
<td>Array</td>
<td>-</td>
<td>[]</td>
</tr>
<tr>
<td>empty-text</td>
<td>内容为空的时候展示的文本</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>node-key</td>
<td>每个树节点用来作为唯一标识的属性，整棵树应该是唯一的</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>props</td>
<td>配置选项，具体看下表</td>
<td>Object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>render-after-expand</td>
<td>是否在第一次展开某个树节点后才渲染其子节点</td>
<td>Boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>load</td>
<td>加载子树数据的方法，仅当 lazy 属性为true 时生效，resolve有两个参数：参数1为子节点数组，参数2为在懒加载后定位的node-key</td>
<td>Function(node, resolve)</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>render-content</td>
<td>树节点的内容区的渲染 Function</td>
<td>Function(h, { node, data, store }</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>highlight-current</td>
<td>是否高亮当前选中节点，默认值是 false</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>expand-on-click-node</td>
<td>是否在点击节点的时候选中节点，默认值为 false，即只有在点击复选框时才会选中节点。</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>auto-expand-parent</td>
<td>展开子节点的时候是否自动展开父节点</td>
<td>Boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>default-expanded-keys</td>
<td>默认展开的节点的 key 的数组</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>show-checkbox</td>
<td>节点是否可被选择</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>check-strictly</td>
<td>在显示复选框的情况下，是否严格的遵循父子不互相关联的做法，默认为 false</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>default-checked-keys</td>
<td>默认勾选的节点的 key 的数组</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>current-node-key</td>
<td>当前选中的节点</td>
<td>String,Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>filter-node-method</td>
<td>对树节点进行筛选时执行的方法，返回 true 表示这个节点可以显示，返回 false 则表示这个节点会被隐藏</td>
<td>Function(value, data, node)</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>accordion</td>
<td>是否每次只打开一个同级树节点展开</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>indent</td>
<td>当前选中的节点</td>
<td>Number</td>
<td>-</td>
<td>16</td>
</tr>
<tr>
<td>icon-class</td>
<td>自定义树节点的图标</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>lazy</td>
<td>是否懒加载子节点，需与 load 方法结合使用</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>show-icon</td>
<td>是否显示节点图标</td>
<td>Boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>show-searchbox</td>
<td>是否显示搜索框</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>search-placeholder</td>
<td>搜索框提示输入的文字</td>
<td>String</td>
<td>-</td>
<td>请输入节点名称...</td>
</tr>
<tr>
<td>highlight-color</td>
<td>高亮节点的背景色</td>
<td>String</td>
<td>-</td>
<td>#F4F9FF</td>
</tr>
<tr>
<td>font-color</td>
<td>节点图标和文字的颜色</td>
<td>String</td>
<td>-</td>
<td>#555555</td>
</tr>
</tbody>
</table>
<h3>Toolbar Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>指定节点标签为节点对象的某个属性值</td>
<td>string, function(data, node)</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>children</td>
<td>指定子树为节点对象的某个属性值</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>指定节点的图标类型，仅在设置 show-icon 属性的情况下生效</td>
<td>folder / share / sharing / file / disable</td>
<td></td>
<td>file</td>
</tr>
<tr>
<td>buttons</td>
<td>指定节点上的操作按钮类型</td>
<td>Array</td>
<td>append / edit / delete / disable / enable</td>
<td>-</td>
</tr>
<tr>
<td>nodeNamePaths</td>
<td>指定显示路径 节点上级名称组成的路径</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>nodePaths</td>
<td>指定标识路径 节点上级id组成的路径</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>hasChildren</td>
<td>指定节点是否为叶子节点，仅在指定了 lazy 属性的情况下生效</td>
<td>Boolean</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>parent</td>
<td>指定父节点的标识</td>
<td>String,Number</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>Toolbar Methods</h3>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>filter</td>
<td>对树节点进行筛选操作</td>
<td>接收一个任意类型的参数，该参数会在 filter-node-method 中作为第一个参数</td>
</tr>
<tr>
<td>updateKeyChildren</td>
<td>通过 keys 设置节点子元素，使用此方法必须设置 node-key 属性</td>
<td>(key, data) 接收两个参数，1. 节点 key 2. 节点数据的数组</td>
</tr>
<tr>
<td>getCheckedNodes</td>
<td>若节点可被选择（即 show-checkbox 为 true），则返回目前被选中的节点所组成的数组</td>
<td>(leafOnly, includeHalfChecked) 接收两个 boolean 类型的参数，1. 是否只是叶子节点，默认值为 false 2. 是否包含半选节点，默认值为 false</td>
</tr>
<tr>
<td>setCheckedNodes</td>
<td>设置目前勾选的节点，使用此方法必须设置 node-key 属性</td>
<td>(nodes) 接收勾选节点数据的数组</td>
</tr>
<tr>
<td>getCheckedKeys</td>
<td>若节点可被选择（即 show-checkbox 为 true），则返回目前被选中的节点的 key 所组成的数组</td>
<td>(leafOnly) 接收一个 boolean 类型的参数，若为 true 则仅返回被选中的叶子节点的 keys，默认值为 false</td>
</tr>
<tr>
<td>setCheckedKeys</td>
<td>通过 keys 设置目前勾选的节点，使用此方法必须设置 node-key 属性</td>
<td>(keys, leafOnly) 接收两个参数，1. 勾选节点的 key 的数组 2. boolean 类型的参数，若为 true 则仅设置叶子节点的选中状态，默认值为 false</td>
</tr>
<tr>
<td>setChecked</td>
<td>通过 key / data 设置某个节点的勾选状态，使用此方法必须设置 node-key 属性</td>
<td>(key/data, checked, deep) 接收三个参数，1. 勾选节点的 key 或者 data 2. boolean 类型，节点是否选中 3. boolean 类型，是否设置子节点 ，默认为 false</td>
</tr>
<tr>
<td>getHalfCheckedNodes</td>
<td>若节点可被选择（即 show-checkbox 为 true），则返回目前半选中的节点所组成的数组</td>
<td>-</td>
</tr>
<tr>
<td>getHalfCheckedKeys</td>
<td>若节点可被选择（即 show-checkbox 为 true），则返回目前半选中的节点的 key 所组成的数组</td>
<td>-</td>
</tr>
<tr>
<td>getCurrentKey</td>
<td>获取当前被选中节点的 key，使用此方法必须设置 node-key 属性，若没有节点被选中则返回 null</td>
<td>-</td>
</tr>
<tr>
<td>getCurrentNode</td>
<td>获取当前被选中节点的 data，若没有节点被选中则返回 null</td>
<td>-</td>
</tr>
<tr>
<td>setCurrentKey</td>
<td>通过 key 设置某个节点的当前选中状态，使用此方法必须设置 node-key 属性</td>
<td>(key) 待被选节点的 key，若为 null 则取消当前高亮的节点</td>
</tr>
<tr>
<td>setCurrentNode</td>
<td>通过 node 设置某个节点的当前选中状态，使用此方法必须设置 node-key 属性</td>
<td>(node) 待被选节点的 node</td>
</tr>
<tr>
<td>getNode</td>
<td>根据 data 或者 key 拿到 Tree 组件中的 node</td>
<td>(data) 要获得 node 的 key 或者 data</td>
</tr>
<tr>
<td>remove(推荐使用removeChild)</td>
<td>删除 Tree 中的一个节点，使用此方法必须设置 node-key 属性</td>
<td>(data) 要删除的节点的 data 或者 node</td>
</tr>
<tr>
<td>removeChild</td>
<td>删除 Tree 中的一个节点，设置其父节点为当前节点，使用此方法必须设置 node-key 属性</td>
<td>(data) 要删除的节点的 data 或者 node</td>
</tr>
<tr>
<td>append(推荐使用appendChild)</td>
<td>为 Tree 中的一个节点追加一个子节点</td>
<td>(data, parentNode) 接收两个参数，1. 要追加的子节点的 data 2. 子节点的 parent 的 data、key 或者 node</td>
</tr>
<tr>
<td>appendChild</td>
<td>为 Tree 中的一个节点追加一个子节点，设置该节点为当前节点</td>
<td>(data, parentNode) 接收两个参数，1. 要追加的子节点的 data 2. 子节点的 parent 的 data、key 或者 node</td>
</tr>
<tr>
<td>insertBefore</td>
<td>为 Tree 的一个节点的前面增加一个节点</td>
<td>(data, refNode) 接收两个参数，1. 要增加的节点的 data 2. 要增加的节点的后一个节点的 data、key 或者 node</td>
</tr>
<tr>
<td>insertAfter</td>
<td>为 Tree 的一个节点的后面增加一个节点</td>
<td>(data, refNode) 接收两个参数，1. 要增加的节点的 data 2. 要增加的节点的前一个节点的 data、key 或者 node</td>
</tr>
<tr>
<td>updateNode</td>
<td>更新节点信息</td>
<td>(node, data) 接收两个参数，1. 要切换的节点 node 2. 切换后的节点新数据 data</td>
</tr>
<tr>
<td>getCurrentKey</td>
<td>更新节点信息</td>
<td>-</td>
</tr>
<tr>
<td>getCurrentKey</td>
<td>获取当前被选中节点的 key，使用此方法必须设置 node-key 属性，若没有节点被选中则返回 null</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>Toolbar Events</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>node-click</td>
<td>节点被点击时的回调e</td>
<td>共三个参数，依次为：传递给 data 属性的数组中该节点所对应的对象、节点对应的 Node、节点组件本身</td>
</tr>
<tr>
<td>node-contextmenu</td>
<td>当某一节点被鼠标右键点击时会触发该事件</td>
<td>共四个参数，依次为：event、传递给 data 属性的数组中该节点所对应的对象、节点对应的 Node、节点组件本身。</td>
</tr>
<tr>
<td>check-change</td>
<td>节点选中状态发生变化时的回调</td>
<td>共三个参数，依次为：传递给 data 属性的数组中该节点所对应的对象、节点本身是否被选中、节点的子树中是否有被选中的节点</td>
</tr>
<tr>
<td>check</td>
<td>当复选框被点击的时候触发</td>
<td>共两个参数，依次为：传递给 data 属性的数组中该节点所对应的对象、树目前的选中状态对象，包含 checkedNodes、checkedKeys、halfCheckedNodes、halfCheckedKeys 四个属性</td>
</tr>
<tr>
<td>current-change</td>
<td>当前选中节点变化时触发的事件</td>
<td>共两个参数，依次为：当前节点的数据，当前节点的 Node 对象</td>
</tr>
<tr>
<td>node-expand</td>
<td>节点被展开时触发的事件</td>
<td>共三个参数，依次为：传递给 data 属性的数组中该节点所对应的对象、节点对应的 Node、节点组件本身</td>
</tr>
<tr>
<td>node-collapse</td>
<td>节点被关闭时触发的事件</td>
<td>共三个参数，依次为：传递给 data 属性的数组中该节点所对应的对象、节点对应的 Node、节点组件本身</td>
</tr>
<tr>
<td>on-search-change</td>
<td>搜索框有输入改变时触发的事件</td>
<td>共两个参数，依次是：输入框改变后的文本 text、搜索后执行的回调函数 resolve，函数参数为一个搜索的节点数组，至少包含label和nodePath属性，参数2在懒加载时候使用</td>
</tr>
<tr>
<td>on-search-selected</td>
<td>仅当树为懒加载时，点击搜索结果列表中的某一项时触发</td>
<td>点击项的数据</td>
</tr>
<tr>
<td>node-append</td>
<td>点击节点上的添加按钮触发的事件</td>
<td>共两个参数，依次为：该节点所对应的对象、节点对应的 Node</td>
</tr>
<tr>
<td>node-edit</td>
<td>点击节点上的编辑按钮触发的事件</td>
<td>共两个参数，依次为：该节点所对应的对象、节点对应的 Node</td>
</tr>
<tr>
<td>node-delete</td>
<td>点击节点上的删除按钮触发的事件</td>
<td>共两个参数，依次为：该节点所对应的对象、节点对应的 Node</td>
</tr>
<tr>
<td>node-disable</td>
<td>点击节点上的禁用按钮触发的事件</td>
<td>共两个参数，依次为：该节点所对应的对象、节点对应的 Node</td>
</tr>
<tr>
<td>node-enable</td>
<td>点击节点上的启用按钮触发的事件</td>
<td>共两个参数，依次为：该节点所对应的对象、节点对应的 Node</td>
</tr>
<tr>
<td>node-moveup</td>
<td>点击节点上的上移按钮触发的事件</td>
<td>共两个参数，依次为：该节点所对应的对象、节点对应的 Node</td>
</tr>
<tr>
<td>node-movedown</td>
<td>点击节点上的下移按钮触发的事件</td>
<td>共两个参数，依次为：该节点所对应的对象、节点对应的 Node</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data () {
    return {
      treeData: [
        {
          id: 1,
          label: '一级 1',
          hasChildren: true,
          children: [{
            id: 4,
            label: '二级 1-1',
            hasChildren: true,
            children: [{
              id: 9,
              label: '三级 1-1-1',
              hasChildren: false
            }, {
              id: 10,
              label: '三级 1-1-2',
              hasChildren: false
            }]
          }]
        }, {
          id: 2,
          label: '一级 2',
          hasChildren: true,
          children: [{
            id: 5,
            label: '二级 2-1',
            hasChildren: false
          }, {
            id: 6,
            label: '二级 2-2',
            hasChildren: false
          }]
        }
      ],
      treeDataList: [
        {
          id: 1,
          label: '一级 1',
          hasChildren: true,
          nodeNamePaths: '一级 1',
          nodePaths: '1',
          parent: null,
        },
        {
          id: 4,
          label: '二级 1-1',
          nodeNamePaths: '一级 1/二级 1-1',
          nodePaths: '1/4',
          hasChildren: true,
          parent: 1
        },
        {
          id: 9,
          label: '三级 1-1-1',
          hasChildren: false,
          nodeNamePaths: '一级 1/二级 1-1/三级 1-1-1',
          nodePaths: '1/4/9',
          parent: 4
        },
        {
          id: 10,
          label: '三级 1-1-2',
          hasChildren: false,
          nodeNamePaths: '一级 1/二级 1-1/三级 1-1-2',
          nodePaths: '1/4/10',
          parent: 4
        },
        {
          id: 2,
          label: '一级 2',
          hasChildren: true,
          nodeNamePaths: '一级 2',
          nodePaths: '2',
          parent: null,
        },
        {
          id: 5,
          label: '二级 2-1',
          hasChildren: false,
          nodeNamePaths: '一级 2/二级 2-1',
          nodePaths: '2/5',
          parent: 2
        },
        {
          id: 6,
          label: '二级 2-2',
          hasChildren: false,
          nodeNamePaths: '一级 2/二级 2-2',
          nodePaths: '2/6',
          parent: 2
        }
      ],
      nodeId: 1,
      currentNodeData: {}
    }
  },
  methods: {
    filterNode(value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    handleSearchChanged (text) {
      this.$refs.filterTree.filter(text)
    },
    loadNode(node, resolve) {
      if (node.level === 0) {
        const data = [{
          id: 1,
          label: '节点 1',
          hasChildren: true
        }]
        return resolve(data)
      }

      if (node.level > 0) {
        this.nodeId++
        const data = [{
          id: this.nodeId,
          label: '节点 ' + this.nodeId,
          hasChildren: true
        }]
        resolve(data)
      }
    },

    handleSearchChanged (text, resolve) {
      const data = this.treeDataList.filter(item => item.label.includes(text))
      resolve(data)
    },
    handleSearchSelected (nodeData, resolve) {
      this.currentNodeData = nodeData
    },
    loadNode2(node, resolve) {
      if (node.level === 0) {
        // data为异步请求得到的数据,此处为模拟
        const data = this.treeDataList.filter(item => item.parent === null)
        return resolve(data)
      }

      if (node.level > 0) {
        // data为异步请求得到的数据,此处为模拟
        const data = this.treeDataList.filter(item => item.parent === node.data.id)
        resolve(data)
      }
    }
  }
}
</script>


