<template>
<div class="toolbar_frame">
  <Button
  v-for="btn in buttons"
  :key="btn.key"
  type="text"
  @click="handleClick(btn.key)"
  :disabled="checkDisabled(btn.key)"
  >{{ btn.name }}</Button>
</div>
</template>

<script>
export default {
  name: 'Toolbar',
  props: {
    buttons: {
      type: Array,
      required: true,
      default () {
        return []
      }
    },
    disables: {
      type: Array,
      required: false,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      disableBtns: []
    }
  },
  methods: {
    handleClick(key) {
      this.$emit(`on-${key}`)
    },
    checkDisabled (name) {
      if (this.disableBtns.includes(name)) {
        return true
      }
      return false
    },
    disableButtons (names) {
      names.forEach(name => {
        if (!this.disableBtns.includes(name)) {
          this.disableBtns.push(name)
        }
      });
    },
    enableButtons (names) {
      names.forEach(name => {
        const index = this.disableBtns.findIndex(item => item === name)
        if (index !== -1) {
          this.disableBtns.splice(index, 1)
        }
      })
    }
  },
  created () {
    this.disableBtns = this.disables
  },
  // watch: {
  //   disables (btns) {
  //     this.disableBtns = btns
  //   }
  // }
}
</script>

<style lang="less">
@toolbarHeight: 32px;
@fontSize: 12px;
.toolbar_frame {
  height: @toolbarHeight;
  border-bottom: 1px solid #DDDDDD;
  background-color: #fff;
  padding-top: 8px;

  .ivu-btn {
    float: left;
    height: 16px;
    line-height: 16px;
    padding: 0 16px;
    border-left: 1px solid #DDDDDD;
    border-radius: 0;
    box-shadow: none;

    > span {
      display: inline-block;
      height: 14px;
      line-height: 14px;
      vertical-align: top;
      font-size: @fontSize;
    }
  }
  .ivu-btn:first-child {
    border-left: none;
  }
}
</style>



