<h2>下拉多选</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;select-tree :options=\&quot;treeData\&quot; node-key=\&quot;id\&quot; multiple v-model=\&quot;selectValue3\&quot;&gt;&lt;/select-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      treeData: [\n        {\n          id: 1,\n          label: '一级 1',\n          hasChildren: true,\n          children: [{\n            id: 4,\n            label: '二级 1-1',\n            hasChildren: true,\n            children: [{\n              id: 9,\n              label: '三级 1-1-1',\n              hasChildren: false\n            }, {\n              id: 10,\n              label: '三级 1-1-2',\n              hasChildren: false\n            }]\n          }]\n        }, {\n          id: 2,\n          label: '一级 2',\n          hasChildren: true,\n          children: [{\n            id: 5,\n            label: '二级 2-1',\n            hasChildren: false\n          }, {\n            id: 6,\n            label: '二级 2-2',\n            hasChildren: false\n          }]\n      }],\n      selectValue3: [],\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
