<h2>API</h2>
<h3>Toolbar Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>options</td>
<td>下拉展示的树数据</td>
<td>Array</td>
<td>-</td>
<td>[]</td>
</tr>
<tr>
<td>value</td>
<td>指定选中项目的 value 值，可以使用 v-model 双向绑定数据。单选时只接受 String 或 Number，多选时只接受 Array</td>
<td>String</td>
<td>Array</td>
<td>-</td>
</tr>
<tr>
<td>node-key</td>
<td>每个树节点用来作为唯一标识的属性，整棵树应该是唯一的</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>clearable</td>
<td>是否可以清空选项，只在单选时有效</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>multiple</td>
<td>是否支持多选</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>searchable</td>
<td>是否支持搜索，只在单选时有效</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用下拉树</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>placeholder</td>
<td>搜索框提示输入的文字</td>
<td>String</td>
<td>-</td>
<td>请输入节点名称...</td>
</tr>
</tbody>
</table>
<h3>Toolbar Events</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>select-change</td>
<td>当选中节点时触发的函数</td>
<td>当为单选模式时，参数为当前选择项的data；当为多选模式时，有两个参数：参数1为选中节点的数组，参数2位当前选择项的data</td>
</tr>
<tr>
<td>on-open</td>
<td>当下拉树打开时触发</td>
<td>-</td>
</tr>
<tr>
<td>on-close</td>
<td>当下拉树收起时触发</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data () {
    return {
      treeData: [
        {
          id: 1,
          label: '一级 1',
          hasChildren: true,
          children: [{
            id: 4,
            label: '二级 1-1',
            hasChildren: true,
            children: [{
              id: 9,
              label: '三级 1-1-1',
              hasChildren: false
            }, {
              id: 10,
              label: '三级 1-1-2',
              hasChildren: false
            }]
          }]
        }, {
          id: 2,
          label: '一级 2',
          hasChildren: true,
          children: [{
            id: 5,
            label: '二级 2-1',
            hasChildren: false
          }, {
            id: 6,
            label: '二级 2-2',
            hasChildren: false
          }]
        }
      ],
      treeData2: [
        {
          id: 1,
          label: '一级 1',
          hasChildren: true,
          children: [{
            id: 4,
            label: '二级 1-1',
            hasChildren: true,
            children: [{
              id: 9,
              label: '三级 1-1-1',
              hasChildren: false
            }, {
              id: 10,
              label: '三级 1-1-2',
              hasChildren: false
            }]
          }]
        }, {
          id: 2,
          label: '一级 2',
          hasChildren: true,
          children: [{
            id: 5,
            label: '二级 2-1',
            hasChildren: false
          }, {
            id: 6,
            label: '二级 2-2',
            hasChildren: false
          }]
        }
      ],
      selectValue: '',
      selectValue2: '',
      selectValue3: []
    }
  }
}
</script>


