<template lang="html">
  <div class="main-frame">
    <Layout style="height:100%">
      <Header height="50">
        <header-model></header-model>
      </Header>
      <Layout>
        <Sider width="240px">
          <menu-model></menu-model>
        </Sider>
        <Layout class="right-content-layout">
          <Content class="scroll-container">
            <router-view></router-view>
          </Content>
          <div class="document-anchor">
            <Anchor show-ink container=".scroll-container">
              <AnchorLink v-for="anchor in anchors" :key="anchor.href" :href="`#${anchor.href}`" :title="anchor.title" />
            </Anchor>
          </div>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>

<style>
/* 引入代码高亮样式 */
@import 'highlight.js/styles/color-brewer.css';
</style>

<script>
import HeaderModel from './components/header'
import MenuModel from './components/menu'
export default {
  components: {
    HeaderModel,
    MenuModel
  },
  data() {
    return {
      anchorsConfig: {
        introduce: [
          { href: 'SM', title: '说明' }
        ],
        toolbar: [
          { href: 'JCYF', title: '基础用法' },
          { href: 'SDJYQY', title: '手动禁用启用' },
          { href: 'API', title: 'API' }
        ],
        basetree: [
          { href: 'JCYF', title: '基础用法' },
          { href: 'KXZ', title: '可选择' },
          { href: 'JDGL', title: '节点过滤' },
          { href: 'LJZ', title: '懒加载' },
          { href: 'YBSSHJDDW', title: '异步搜索和节点定位' },
          { href: 'API', title: 'API' }
        ],
        modaltree: [
          { href: 'TCQJZ', title: '弹出全加载' },
          { href: 'TCLJZ', title: '弹出懒加载' },
          { href: 'API', title: 'API' }
        ],
        selecttree: [
          { href: 'XLDX', title: '下拉单选' },
          { href: 'XLKSS', title: '下拉可搜索' },
          { href: 'XLDUOX', title: '下拉多选' },
          { href: 'API', title: 'API' }
        ]
      },
      anchors: []
    }
  },
  methods: {
  },
  mounted () {
    this.anchors = this.anchorsConfig['introduce']
    this.$nextTick(() => {
      const anchorDom = document.querySelectorAll('h2')
      anchorDom.forEach((item, index) => {
        item.setAttribute('id', this.anchorsConfig['introduce'][index].href)
      })
    })
    this.$router.push('/introduce')
  },
  watch: {
    $route (to, from) {
      if (to.name === from.name) return
      this.anchors = this.anchorsConfig[to.name]
      this.$nextTick(() => {
        const anchorDom = document.querySelectorAll('h2')
        anchorDom.forEach((item, index) => {
          item.setAttribute('id', this.anchorsConfig[to.name][index].href)
        })
      })

    }
  }
}
</script>

<style>
.main-frame {
  height: 100vh;
  overflow: hidden;
}

.main-frame .demo-block {
  margin-bottom: 30px;
}

.main-frame .ivu-layout {
  height: 100%;
  background-color: #fff;
  flex: 1;
}

.main-frame .right-content-layout {
  flex-direction: row;
}

.main-frame .ivu-layout-header {
  background-color: #fff;
  border-bottom: 1px solid #ddd;
  padding: 0 20px;
}

.main-frame .ivu-layout-content {
  overflow: auto;
  padding: 10px 20px;
  background-color: #fff;
}

.main-frame .document-anchor {
  width: 250px;
  background-color: #fff;
  margin-top: 10px;
  padding-left: 20px;
}

h1, h2, h3 {
  margin: 12px 0;
}
h1 {
  font-weight: 500;
}
</style>




