<template lang="html">
  <div class="menu-model">
    <Menu active-name="1-1">
      <MenuGroup title="开发指南">
        <MenuItem name="1-1" to="/guide/introduce">
          特别说明
        </MenuItem>
      </MenuGroup>
      <MenuGroup title="组件">
        <MenuItem name="2-1" to="/toolbar">
          Toolbar 工具栏
        </MenuItem>
        <MenuItem name="2-2" to="/basetree">
          BaseTree 基础树
        </MenuItem>
        <MenuItem name="2-3" to="/modaltree">
          ModalTree 弹出树
        </MenuItem>
        <MenuItem name="2-4" to="/selecttree">
          SelectTree 下拉树
        </MenuItem>
      </MenuGroup>
    </Menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      defaultActive: '/guide/install'
    }
  },
  created() {
    const path = this.$route.fullPath
    this.defaultActive = path == '/' ? '/guide/install' : path
  },
  methods: {}
}
</script>

<style lang="css">
.menu-model {
  height: 100;
}

.menu-model .ivu-menu {
  height: 100%;
}
</style>



