import Input from './components/input'
import ButtonModel from './components/buttonModel'
import Toolbar from './components/toolbar'
import BaseTree from './components/trees/base-tree'
import ModalTree from './components/trees/modal-tree'
import SelectTree from './components/trees/select-tree'

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(install)
}

function install (Vue) {
  Vue.component(Input.name, Input)
  Vue.component(ButtonModel.name, ButtonModel)
  Vue.component(Toolbar.name, Toolbar)
  Vue.component(BaseTree.name, BaseTree)
  Vue.component(ModalTree.name, ModalTree)
  Vue.component(SelectTree.name, SelectTree)
}

export default {
  install,
  Input,
  ButtonModel,
  Toolbar,
  BaseTree,
  ModalTree,
  SelectTree
}



