<template>
  <div
  class="acct-tree-node"
  @click.stop="handleClick"
  @contextmenu="($event) => this.handleContextMenu($event)"
  v-show="node.visible"
  :class="{
    'is-expanded': expanded,
    'is-current': node.isCurrent,
    'is-hidden': !node.visible,
    'is-focusable': !node.disabled,
    'is-checked': !node.disabled && node.checked,
    'is-selected': node.data.selectedInTree
  }"
  role="treeitem"
  tabindex="-1"
  :aria-expanded="expanded"
  :aria-disabled="node.disabled"
  :aria-checked="node.checked"
  :draggable="tree.draggable"
  @dragstart.stop="handleDragStart"
  @dragover.stop="handleDragOver"
  @dragend.stop="handleDragEnd"
  @drop.stop="handleDrop"
  ref="node"
  >
    <div :class="{
      'acct-tree-node__content': true,
      'is-leaf': node.isLeaf || !node.data.hasChildren,
      }"
      :style="{
        'padding-left': calcIndent(node.level, tree.indent),
        'background-color': calcNodeBackground(node, highlightColor)
      }"
      @mouseover="handleNodeMouseOver(highlightColor)"
      @mouseout="handleNodeMouseOut(node)"
    >
      <span :class="{
        'before-line': true,
        'last-before-line': calcLastBeforeLine(node, index)
      }" v-for="(item, index) in calcLines(node)" :key="index">
      </span>
      <!-- 'last-before-line': calcLastBeforeLine(node, index) -->
      <!-- 'last-node-line': calcLines(node).length > 1 && index === calcLines(node).length - 1 && !node.nextSibling -->
      <span :class="{
        'node-line': true,
        'last-node-line': calcLastLine(node),
        'leaf-node-line': !node.data.hasChildren
      }" v-if="node.level !== 1">
        <span class="node-line-inner"></span>
      </span>
      <span
      @click.stop="handleExpandIconClick"
      :class="[
        { 'is-leaf': node.isLeaf || !node.data.hasChildren, expanded: !node.isLeaf && expanded },
        'acct-tree-node__expand-icon',
        tree.iconClass ? tree.iconClass : 'ivu-icon ivu-icon-md-arrow-dropright',
      ]"
      >
      </span>
      <acct-checkbox
      v-if="showCheckbox"
      v-model="node.checked"
      :indeterminate="node.indeterminate"
      :disabled="!!node.disabled"
      @click.native.stop
      @on-change="handleCheckChange"
      >
      </acct-checkbox>
      <span v-if="showIcon" class="acct-node-icon">
        <!-- <img :src="calcIconByType(node.data)" :style="{ 'filter': `drop-shadow(-20px 0 ${calcFontColor(node, fontColor, true)})` }" /> -->
        <!-- <svg class="icon" aria-hidden="true">
          <use :xlink:href="calcIconByType(node.data)"></use>
        </svg> -->
        <i :class="calcIconByType2(node.data)" :style="{ 'color': calcFontColor(node, fontColor, true) }"></i>
      </span>
      <span
      v-if="node.loading"
      class="acct-tree-node__loading-icon acct-icon-loading">
      </span>
      <node-content v-else :node="node" :font-color="fontColor" :color-func="calcFontColor"></node-content>
      <span
        class="acct-node-btns"
        v-if="showOperateButtons(node.data.buttons)"
        :style="{ 'background-color': highlightColor }"
      >
        <Button
          v-for="btn in calcOperateButtons(node.data.buttons)"
          :key="btn.type"
          :class="btn.type"
          :custom-icon="btn.icon"
          @click.stop="handleOperateNode(btn.type, node)"
        ></Button>
      </span>
      <Icon type="md-checkmark" class="acct-select-icon" v-if="node.data.selectedInTree" />
    </div>
    <acct-collapse-transition>
      <div
      class="acct-tree-node__children"
      v-if="!renderAfterExpand || childNodeRendered"
      v-show="expanded"
      role="group"
      :aria-expanded="expanded"
      >
      <!-- <span class="acct-children-line" :style="{ left: (node.level - 1) * tree.indent + 16 + 'px' }">
        <span class="acct-children-line-inner"></span>
      </span> -->
      <acct-tree-node
        :render-content="renderContent"
        v-for="child in node.childNodes"
        :render-after-expand="renderAfterExpand"
        :show-checkbox="showCheckbox"
        :show-icon="showIcon"
        :show-buttons="showButtons"
        :key="getNodeKey(child)"
        :node="child"
        :highlight-color="highlightColor"
        :font-color="fontColor"
        @node-expand="handleChildNodeExpand">
      </acct-tree-node>
      </div>
    </acct-collapse-transition>
  </div>
</template>

<script type="text/jsx">
import AcctCollapseTransition from 'iview/src/components/base/collapse-transition';
import AcctCheckbox from 'iview/src/components/checkbox';
import emitter from 'iview/src/mixins/emitter';
import { getNodeKey } from './src/js/util';

export default {
  name: 'AcctTreeNode',

  componentName: 'AcctTreeNode',

  mixins: [emitter],

  props: {
    node: {
      default () {
        return {};
      }
    },
    props: {},
    renderContent: Function,
    renderAfterExpand: {
      type: Boolean,
      default: true
    },
    showCheckbox: {
      type: Boolean,
      default: false
    },
    showIcon: {
      type: Boolean,
      default: true
    },
    showButtons: {
      type: Boolean,
      default: true
    },
    highlightColor: {
      type: String,
      default: '#F4F9FF'
    },
    fontColor: {
      type: String,
      default: '#555555'
    }
  },

  components: {
    AcctCollapseTransition,
    AcctCheckbox,
    // ElInput,
    NodeContent: {
      props: {
        node: {
          required: true
        },
        fontColor: {
          type: String
        },
        colorFunc: {
          type: Function
        }
      },
      render (h) {
        const parent = this.$parent;
        const tree = parent.tree;
        const node = this.node;
        const { data, store } = node;
        return (
          parent.renderContent
            ? parent.renderContent.call(parent._renderProxy, h, { _self: tree.$vnode.context, node, data, store })
            : tree.$scopedSlots.default
              ? tree.$scopedSlots.default({ node, data })
              : h ('span', {
                class: 'acct-tree-node__label',
                style: {
                  color: `${this.colorFunc(node, this.fontColor, false)}`
                }
              }, node.label)
              // : <span class="acct-tree-node__label" style="{ 'font-color': `${calcFontColor(node, fontColor, false)}` }">{ node.label }</span>
        );
      }
    }
  },

  data () {
    return {
      tree: null,
      expanded: false,
      childNodeRendered: false,
      oldChecked: null,
      oldIndeterminate: null,
      // 节点类型图标
      nodeIcons: {
        folder: 'folder',
        share: 'share',
        sharing: 'sharing',
        file: 'file',
        disable: 'disable'
      },
      // 节点操作按钮
      operateButtons: {
        append: 'append',
        edit: 'edit',
        delete: 'delete',
        disable: 'disable',
        enable: 'enable',
        movedown: 'movedown',
        moveup: 'moveup'
      },
      // 当前按下的按键
      pressKey: null,
      nodeButtonsRigth: 0
    };
  },

  watch: {
    'node.indeterminate' (val) {
      this.handleSelectChange(this.node.checked, val);
    },

    'node.checked' (val) {
      this.handleSelectChange(val, this.node.indeterminate);
    },

    'node.expanded' (val) {
      this.$nextTick(() => {
        this.expanded = val;
        return this.expanded;
      });
      if (val) {
        this.childNodeRendered = true;
      }
    }
  },

  methods: {
    getNodeKey (node) {
      return getNodeKey(this.tree.nodeKey, node.data);
    },

    handleSelectChange (checked, indeterminate) {
      if (this.oldChecked !== checked && this.oldIndeterminate !== indeterminate) {
        this.tree.$emit('check-change', this.node.data, checked, indeterminate);
      }
      this.oldChecked = checked;
      this.indeterminate = indeterminate;
    },

    handleClick () {
      const store = this.tree.store;
      store.setCurrentNode(this.node);
      this.tree.$emit('current-change', store.currentNode ? store.currentNode.data : null, store.currentNode);
      this.tree.currentNode = this;
      if (this.tree.expandOnClickNode) {
        this.handleExpandIconClick();
      }
      if (this.tree.checkOnClickNode && !this.node.disabled) {
        this.handleCheckChange(null, {
          target: { checked: !this.node.checked }
        });
      }
      this.tree.$emit('node-click', this.node.data, this.node, this);
    },

    handleContextMenu (event) {
      if (this.tree._events['node-contextmenu'] && this.tree._events['node-contextmenu'].length > 0) {
        event.stopPropagation();
        event.preventDefault();
      }
      this.tree.$emit('node-contextmenu', event, this.node.data, this.node, this);
    },

    handleExpandIconClick () {
      if (this.node.isLeaf) return;
      if (this.expanded) {
        this.tree.$emit('node-collapse', this.node.data, this.node, this);
        this.node.collapse();
      } else {
        this.node.expand();
        this.$emit('node-expand', this.node.data, this.node, this);
      }
    },

    handleCheckChange (ev) {
      // console.log(document.pressKey)
      if (document.pressKey === 'Ctrl') {
        this.node.keyFlag = 'Ctrl'
        this.node.setChecked(ev, !this.tree.checkStrictly);
      } else if (document.pressKey === 'Shift') {
        this.node.keyFlag = 'Shift'
        this.node.setChecked(ev, !this.tree.checkStrictly);
      } else {
        this.node.setChecked(ev, this.tree.checkStrictly)
      }

      this.$nextTick(() => {
        // const store = this.tree.store;
        this.tree.$emit('check', this.node);
      });
    },

    handleNodeMouseOver (color) {
      this.$el.children[0].style.backgroundColor = color
    },

    handleNodeMouseOut (node) {
      if (node.isCurrent) return
      this.$el.children[0].style.backgroundColor = 'rgba(0, 0, 0, 0)'
    },

    handleKeyPress (key) {
    },

    handleChildNodeExpand (nodeData, node, instance) {
      this.broadcast('AcctTreeNode', 'tree-node-expand', node);
      this.tree.$emit('node-expand', nodeData, node, instance);
    },

    handleDragStart (event) {
      if (!this.tree.draggable) return;
      this.tree.$emit('tree-node-drag-start', event, this);
    },

    handleDragOver (event) {
      if (!this.tree.draggable) return;
      this.tree.$emit('tree-node-drag-over', event, this);
      event.preventDefault();
    },

    handleDrop (event) {
      event.preventDefault();
    },

    handleDragEnd (event) {
      if (!this.tree.draggable) return;
      this.tree.$emit('tree-node-drag-end', event, this);
    },

    handleOperateNode (type, node) {
      this.tree.$emit(`node-${type}`, node.data, node);

      // 上移
      if (type === 'moveup') {
        const prev = node.previousSibling;
        this.tree.removeChild(node);
        this.tree.insertBefore(node.data, prev, node.id);
      }

      // 下移
      if (type === 'movedown') {
        const next = node.nextSibling;
        this.tree.removeChild(node);
        this.tree.insertAfter(node.data, next, node.id);
        console.log(this.tree)
      }
    },

    calcIconByType (data) {
      if (!data.icon) {
        if (data.hasChildren) {
          return require('./src/icons/folder.svg');
        } else {
          return require('./src/icons/file.svg');
        }
      }
      return require(`./src/icons/${data.icon}.svg`);
    },

    calcIconByType2 (data) {
      if (!data.icon) {
        if (data.hasChildren) {
          return `iconfont icon-folder`;
        } else {
          return `iconfont icon-file`;
        }
      }
      return `iconfont icon-${data.icon}`;
    },

    calcFontColor (node, color, isIcon) {
      if (node.isCurrent) {
        return color
      }
      if (isIcon) {
        return '#888888'
      }
      return '#555555'
    },

    calcOperateButtons (data) {
      if (!data) {
        data = [];
      }
      let btnsData = [];
      data.forEach(item => {
        let temp = {};
        temp.type = item;
        temp.icon = `iconfont icon-${this.operateButtons[item]}`;
        btnsData.push(temp);
      });
      return btnsData;
    },

    calcIndent (level, indent) {
      if (level > 1) {
        return indent + 'px'
      }
      return '10px'
    },

    calcNodeBackground (node, color) {
      if (node.isCurrent) {
        return color;
      } else {
        return 'transparent'
      }

    },

    calcLastBeforeLine (node, index) {
      // console.log(index)
      // for(let i = lines.length - 1; i > 0; i--) {
      //   if ((!node || !node.nextSibling) && (node || !node.parentNode.nextSibling)) {
      //     return true
      //   } else {
      //     return false
      //   }
      // }
      // function parent (n) {
      //   return n.parentNode
      // }
      const lines = this.calcLines(node)
      let tempNode = node
      for (let i = 0; i < lines.length - index; i++) {
        // if (tempNode && tempNode.parentNode) {
          tempNode = tempNode.parent
        // }
      }
      if (!node.nextSibling && !node.expanded && !tempNode.nextSibling) {
        return true
      } else {
        return false
      }
      // console.log(tempNode)
      // if (!tempNode.parentNode) {
      //   if (tempNode.nextSibling) {
      //     return false
      //   } else {
      //     return true
      //   }
      // }

      // if (!tempNode.nextSibling && !tempNode.parentNode.nextSibling) {
      //   return true
      // } else {
      //   return false
      // }
    },

    calcLastLine (node) {
      let flag = false
      if (!node.nextSibling) {
        if (!node.data.hasChildren) {
          flag = true
        } else {
          node.expanded ? flag = false : flag = true
        }
      }
      return flag
    },

    calcLines (node) {
      // console.log(node)
      const level = node.level
      let lines = []
      if (level > 2) {
        for(let i = 0; i < level - 2; i++) {
          lines.push(i)
        }
      }
      return lines
    },

    showOperateButtons (btns) {
      if (!this.showButtons) return false
      if (btns && Array.isArray(btns)) {
        if (btns.length > 0) {
          return true
        }
      }
      return false
    }
  },

  created () {
    const parent = this.$parent;

    if (parent.isTree) {
      this.tree = parent;
    } else {
      this.tree = parent.tree;
    }

    const tree = this.tree;
    if (!tree) {
      console.warn('Can not find node\'s tree.');
    }

    const props = tree.props || {};
    const childrenKey = props['children'] || 'children';

    this.$watch(`node.data.${childrenKey}`, () => {
      this.node.updateChildren();
    });

    if (this.node.expanded) {
      this.expanded = true;
      this.childNodeRendered = true;
    }

    if (this.tree.accordion) {
      this.$on('tree-node-expand', node => {
        if (this.node !== node) {
          this.node.collapse();
        }
      });
    }
  },
  mounted () {
    document.pressKey = null;
    document.onkeydown = (e) => {
      if (e.keyCode === 17) { // Ctrl
        document.pressKey = 'Ctrl';
      } else if (e.keyCode === 16) { // Shift
        document.pressKey = 'Shift';
      }
      // console.log(this.pressKey)
    }

    document.onkeyup = (e) => {
      document.pressKey = null;
    }
  }
};
</script>

<style>
/* .acct-tree-node::before {
  content: '';
  width: 1px;
  height: 30px;
  background-color: #ccc;
} */

/* .acct-tree-node__content {
  position: relative;
} */

/* .acct-tree-node__content::before {
  content: '';
  position: absolute;
  top: 15px;
  display: block;
  width: 10px;
  height: 1px;
  border-bottom: 1px dashed #ccc;
} */

.acct-tree-node__content.is-leaf::before {
  width: 20px;
}

.acct-tree-node.is-current > .acct-tree-node__content {
  background-color: transparent;
}

.acct-tree-node__content > .acct-tree-node__expand-icon {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 19px;
  height: 24px;
  padding: 0;
  /* margin-left: 5px; */
  font-size: 16px;
  color: #888888;
}

/* .acct-tree-node__children {
  position: relative;
} */

/* .acct-children-line {
  position: absolute;
  z-index: 10;
  width: 10px;
  height: 100%;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
}

.acct-children-line-inner {
  display: block;
  flex: 1;
  border-left: 1px dashed #ccc;
}

.acct-children-line-empty {
  display: block;
} */

.acct-tree .before-line {
  display: block;
  width: 18px;
  height: 30px;
  background: url('./src/icons/line2.png') no-repeat left top;
}

.acct-tree .before-line.last-before-line {
  background: url('./src/icons/line4.png') no-repeat left top;
}

.acct-tree .node-line {
  display: block;
  width: 10px;
  height: 30px;
  overflow: visible;
  position: relative;
}

.acct-tree .node-line-inner {
  position: absolute;
  left: 0;
  top: 0;
  width: 10px;
  height: 30px;
  background: url('./src/icons/line.png') no-repeat left top;
}

.acct-tree .node-line.last-node-line .node-line-inner {
  background: url('./src/icons/line5.png') no-repeat left top;
}

.acct-tree .node-line.leaf-node-line .node-line-inner {
  width: 18px;
  background: url('./src/icons/line3.png') no-repeat left top;
}

.acct-tree .node-line.last-node-line.leaf-node-line .node-line-inner {
  width: 18px;
  background: url('./src/icons/line4.png') no-repeat left top;
}

.acct-node-icon {
  display: flex;
  align-items: center;
  /* margin-right: 6px; */
  padding-bottom: 5px;
  width: 20px;
  height: 20px;
  position: relative;
  overflow: hidden;
}

.acct-node-icon img {
  display: block;
  position: absolute;
  width: 34px;
  height: 14px;
  border-left: 20px solid transparent;
  filter: drop-shadow(-20px 0 #555555);
}

.acct-node-icon i {
  display: block;
  width: 14px;
  height: 14px;
  font-size: 14px;
}

.acct-tree-node__content:hover .acct-node-btns {
  opacity: 0.9;
}

.acct-node-btns {
  display: flex;
  align-items: center;
  z-index: 10;
  position: absolute;
  right: 0;
  height: 30px;
  padding: 0 6px 0 18px;
  background-color:rgba(244, 249, 255, 1);
  opacity: 0;
}

.acct-node-btns .ivu-btn {
  margin-right: 6px;
  padding: 3px;
  border: none;
  line-height: 14px;
  background-color: transparent;
}

.acct-node-btns .ivu-btn:focus {
  background-color: transparent;
  outline: none;
  box-shadow: none;
}

.acct-node-btns .enable i {
  color: #52C41A;
}

.acct-node-btns .add:hover i,
.acct-node-btns .edit:hover i {
  color: #4880FF;
}

.acct-node-btns .delete:hover i {
  color: #F5222D;
}

.acct-node-btns .forbid:hover i {
  color: #FAAD14;
}

.acct-node-btns .ivu-btn .iconfont {
  font-size: 14px;
  line-height: 14px;
}

.acct-node-icon svg.icon {
  font-size: 14px;
}

.acct-node-icon .iconfont {
  font-size: 14px;
}

.acct-node-icon .forbid-icon {
  color: #FAAD14;
}

</style>



