<template>
<Modal
:class-name="'common-setting'"
width="960"
v-model="modalVisible"
title="常用设置"
@on-visible-change="handleVisibleChanged"
>
  <Card class="left-tree">
    <p slot="title">所有条目</p>
    <div class="search-box">
      <Input
      search v-model="allSearchText"
      placeholder="请输入关键字搜索"
      @on-change="handleAllSearchChanged"
      ></Input>
    </div>
    <div :class="{'tree-box': true, 'tree-searching': allSearching}" >
      <base-tree
      ref="allTree"
      :data="data"
      :show-checkbox="true"
      :show-buttons="false"
      :lazy="lazy"
      :load="load"
      node-key="label"
      :filter-node-method="filterNode"
      ></base-tree>
    </div>
    <div :class="{'search-tree-box': true, 'tree-searching': allSearching}">
      <div ref="searchList" class="acct-search-list">
        <Checkbox
        :label="item[nodeKey]"
        class="acct-search-item"
        v-for="(item, index) in allSearchResult"
        :key="item[nodeKey]"
        @on-change="check => handleLeftChecked(check, item)"
        >
          <span class="acct-search-label">{{ item.label }}</span>
        </Checkbox>
      </div>
    </div>
  </Card>
  <div class="center-box">
    <Button size="small" class="select" @click="handleTransformNodes">选择</Button>
    <Button size="small" @click="handleDeleteNodes">删除</Button>
  </div>
  <Card>
    <p slot="title">常用条目</p>
    <div class="search-box">
      <Input
      search v-model="targetSearchText"
      placeholder="请输入关键字搜索"
      @on-change="handleTargetSearchChanged"
      ></Input>
    </div>
    <div class="tree-box">
      <base-tree
      ref="targetTree"
      :node-key="nodeKey"
      :data="selectNodes"
      :show-checkbox="true"
      :show-buttons="false"
      :default-expand-all="true"
      :filter-node-method="filterNode"
      ></base-tree>
    </div>
  </Card>

  <div slot="footer">
    <Button type="text" @click="modalVisible = false">取消</Button>
    <Button type="primary" @click="handleOkClicked">确定</Button>
  </div>
</Modal>
</template>

<script>
import BaseTree from '../base-tree'
export default {
  name: 'CommonSetting',
  components: {
    BaseTree
  },
  props: {
    data: {
      type: Array,
      required: true,
      default () {
        return []
      }
    },
    nodeKey: {
      type: String,
      required: true
    },
    lazy: {
      type: Boolean,
      default: false
    },
    load: Function
  },
  data () {
    return {
      modalVisible: false,
      allSearchText: '',
      targetSearchText: '',
      selectNodes: [],
      defaultExpandAll: false,
      allSearching: false,
      allSearchResult: [],
      checkedObj: {},
      checkedArray: []
    }
  },
  methods: {
    filterNode (value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1;
    },
    setModalVisible () {
      this.modalVisible = true
    },
    handleOkClicked () {
      this.$emit('on-transferred', this.selectNodes)
      this.modalVisible = false
    },
    handleVisibleChanged (visible) {
      if (visible) {
        this.selectNodes = []
      }
    },
    handleAllSearchChanged () {
      if (this.lazy) {
        if (this.allSearchText === '') {
          this.allSearching = false
        } else {
          this.allSearching = true
          this.$emit('on-left-search', this.allSearchText, this.resolveAllResult)
        }
      } else {
        this.$refs.allTree.filter(this.allSearchText)
      }
    },
    handleTargetSearchChanged () {
      this.$refs.targetTree.filter(this.targetSearchText)
    },
    handleTransformNodes () {
      if (this.allSearching) {
        let checkedArray = []
        Object.keys(this.checkedObj).forEach(key => {
          this.checkedObj[key].forEach(ary => {
            if(!checkedArray.find(item => item[this.nodeKey] === ary[this.nodeKey])) {
              checkedArray.push(ary)
            }
          })
        })
        let nodes = this.formatArrayToTree(checkedArray)
        this.selectNodes = JSON.parse(JSON.stringify(nodes))
      } else {
        let selectArray = this.$refs.allTree.getCheckedNodes(false, true)
        let nodes = this.formatArrayToTree(selectArray)
        this.selectNodes = JSON.parse(JSON.stringify(nodes))
      }
    },
    handleDeleteNodes () {
      let selectArray = this.$refs.targetTree.getCheckedNodes()
      for (let i = selectArray.length -1; i >= 0; i--) {
        this.$refs.targetTree.setChecked(selectArray[i], false)
        this.$refs.targetTree.removeChild(selectArray[i])
      }
    },
    handleLeftChecked (check, item) {
      if (check) {
        this.$emit('on-left-checked', item[this.nodeKey], this.leftChecked)
      } else {
        delete this.checkedObj[item[this.nodeKey]]
      }
    },
    formatArrayToTree (array) {
      let nodes = []
      for (let i = 0; i < array.length; i++) {
        if (array[i].parentId === null) {
          let node = JSON.parse(JSON.stringify(array[i]))
          array[i] = JSON.parse(JSON.stringify(array[array.length - 1]))
          array[array.length - 1] = null
          array.length--
          i--
          node.children = []
          this.getChildren(node, array)
          nodes.push(node)
        }
      }
      return nodes
    },
    getChildren (node, otherNodes) {
      for (let i = 0; i < otherNodes.length; i++) {
        if (node[this.nodeKey] === otherNodes[i].parentId) {
          let tempNode = JSON.parse(JSON.stringify(otherNodes[i]))
          otherNodes[i] = JSON.parse(JSON.stringify(otherNodes[otherNodes.length - 1]))
          otherNodes[otherNodes.length - 1] = null
          otherNodes.length--
          i--
          node.children.push(tempNode)
          if (tempNode.hasChildren) {
            tempNode.children = []
            this.getChildren(tempNode, otherNodes)
          }
        }
      }
    },
    resolveAllResult (data) {
      this.allSearchResult = data
    },
    leftChecked (array) {
      let key = array[array.length - 1][this.nodeKey]
      this.checkedObj[key] = array
    }
  },
  mounted () {
    console.log(this.$refs.allTree)
  }
}
</script>

<style>
.common-setting .ivu-modal-body {
  display: flex;
}

.common-setting .ivu-card {
  width: 428px;
  border-radius: 0;
  box-shadow: none;
}

.common-setting .ivu-card .ivu-card-head {
  padding: 8px 16px;
  background-color: #F5F5F5;
}

.common-setting .ivu-card .ivu-card-head p {
  font-weight: 400;
}

.common-setting .left-tree .tree-box.tree-searching {
  display: none;
}

.common-setting .left-tree .search-tree-box.tree-searching {
  display: block;
}

.common-setting .tree-box {
  height: 375px;
  margin-top: 10px;
}

.common-setting .search-tree-box {
  display: none;
  height: 375px;
  margin-top: 10px;
  overflow-y: auto;
}

.common-setting .center-box {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.common-setting .center-box .select {
  margin-bottom: 10px;
}

.common-setting .search-tree-box .acct-search-list {
  /* position: absolute;
  top: 44px; */
  /* left: 10px; */
  /* z-index: 999; */
  /* min-width: calc(100% - 20px); */
  margin: 0;
  padding: 6px 0;
  /* border: 1px solid #ddd; */
  /* border-radius: 4px; */
  box-sizing: border-box;
  background-color: #fff;
  list-style: none;
}

.common-setting .search-tree-box .acct-search-list .acct-search-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 7px 10px;
  box-sizing: border-box;
  /* line-height: 16px; */
}

.common-setting .search-tree-box .acct-search-list .acct-search-item.selected {
  background-color: #F4F9FF;
}

.common-setting .search-tree-box .acct-search-list .acct-search-item .acct-search-label {
  /* width: 200px; */
  margin-right: 20px;
  line-height: 14px;
  white-space: nowrap;
}

.common-setting .search-tree-box .acct-search-list .acct-search-item .acct-search-path {
  /* white-space: nowrap; */
  color: #aaa;
}

.common-setting .search-tree-box .acct-search-list .acct-search-item:hover {
  background-color: #F4F9FF;
  cursor: pointer;
}

.common-setting .search-tree-box .acct-search-list .acct-search-item .ivu-checkbox {
  display: block;
  height: 14px;
  line-height: 14px;
  margin-right: 5px;
}
</style>





