<template><section>
<h1>弹出树</h1>
<p>关于弹出树与树有关的属性和方法，请参照base-tree的用法。</p>
<h2>弹出全加载</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;modal-tree :data=\&quot;treeData\&quot; node-key=\&quot;id\&quot; entry-title=\&quot;科目\&quot; v-model=\&quot;defaultValue\&quot;&gt;&lt;/modal-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      treeData: [\n        {\n          id: 1,\n          label: '一级 1',\n          hasChildren: true,\n          children: [{\n            id: 4,\n            label: '二级 1-1',\n            hasChildren: true,\n            children: [{\n              id: 9,\n              label: '三级 1-1-1',\n              hasChildren: false\n            }, {\n              id: 10,\n              label: '三级 1-1-2',\n              hasChildren: false\n            }]\n          }]\n        }, {\n          id: 2,\n          label: '一级 2',\n          hasChildren: true,\n          children: [{\n            id: 5,\n            label: '二级 2-1',\n            hasChildren: false\n          }, {\n            id: 6,\n            label: '二级 2-2',\n            hasChildren: false\n          }]\n      }],\n      defaultValue: '',\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <modal-tree :data="treeData" node-key="id" entry-title="科目" v-model="defaultValue"></modal-tree>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
