  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">modal-tree</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">treeData</span>: [
        {
          <span class="hljs-attr">id</span>: <span class="hljs-number">1</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'一级 1'</span>,
          <span class="hljs-attr">hasChildren</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">children</span>: [{
