</div></demo-block>
<h2>弹出懒加载</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;modal-tree :data=\&quot;modalTreeLazyData\&quot; entry-title=\&quot;科目\&quot; v-model=\&quot;defaultValue2\&quot; :lazy=\&quot;true\&quot; :load=\&quot;loadNodes\&quot; node-key=\&quot;id\&quot; @on-search-change=\&quot;handleLazySearch\&quot;&gt;&lt;/modal-tree&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      modalTreeLazyData: [],\n      defaultValue2: '',\n    }\n  },\n  methods: {\n    loadNodes(node, resolve) {\n      if (node.level === 0) {\n        // data为异步请求得到的数据,此处为模拟\n        const data = this.treeDataList.filter(item =&gt; item.parent === null)\n        return resolve(data)\n      }\n\n      if (node.level &gt; 0) {\n        // data为异步请求得到的数据,此处为模拟\n        const data = this.treeDataList.filter(item =&gt; item.parent === node.data.id)\n        // 提供第二个参数，能更好的高亮展开选中节点\n        resolve(data, this.currentNodeData.id)\n      }\n    },\n    handleLazySearch (label, resolve) {\n      const data = this.treeDataList.filter(item =&gt; item.label.includes(label))\n      resolve(data)\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <modal-tree :data="modalTreeLazyData" entry-title="科目" v-model="defaultValue2" :lazy="true" :load="loadNodes" node-key="id" @on-search-change="handleLazySearch"></modal-tree>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">modal-tree</span>
    <span class="hljs-attr">:data</span>=<span class="hljs-string">"modalTreeLazyData"</span>
    <span class="hljs-attr">entry-title</span>=<span class="hljs-string">"科目"</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"defaultValue2"</span>
    <span class="hljs-attr">:lazy</span>=<span class="hljs-string">"true"</span>
    <span class="hljs-attr">:load</span>=<span class="hljs-string">"loadNodes"</span>
    <span class="hljs-attr">node-key</span>=<span class="hljs-string">"id"</span>
    <span class="hljs-attr">@on-search-change</span>=<span class="hljs-string">"handleLazySearch"</span>
  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">modal-tree</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">modalTreeLazyData</span>: [],
      <span class="hljs-attr">defaultValue2</span>: <span class="hljs-string">''</span>,
    }
  },
  <span class="hljs-attr">methods</span>: {
    loadNodes(node, resolve) {
      <span class="hljs-keyword">if</span> (node.level === <span class="hljs-number">0</span>) {
        <span class="hljs-comment">// data为异步请求得到的数据,此处为模拟</span>
        <span class="hljs-keyword">const</span> data = <span class="hljs-keyword">this</span>.treeDataList.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> item.parent === <span class="hljs-literal">null</span>)
        <span class="hljs-keyword">return</span> resolve(data)
      }

      <span class="hljs-keyword">if</span> (node.level &gt; <span class="hljs-number">0</span>) {
        <span class="hljs-comment">// data为异步请求得到的数据,此处为模拟</span>
        <span class="hljs-keyword">const</span> data = <span class="hljs-keyword">this</span>.treeDataList.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> item.parent === node.data.id)
        <span class="hljs-comment">// 提供第二个参数，能更好的高亮展开选中节点</span>
        resolve(data, <span class="hljs-keyword">this</span>.currentNodeData.id)
      }
    },
    handleLazySearch (label, resolve) {
      <span class="hljs-keyword">const</span> data = <span class="hljs-keyword">this</span>.treeDataList.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> item.label.includes(label))
      resolve(data)
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>API</h2>
<h3>Toolbar Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>optdataions</td>
<td>弹出展示的树数据</td>
<td>Array</td>
<td>-</td>
<td>[]</td>
</tr>
<tr>
<td>value</td>
<td>指定选中项目的 value 值</td>
<td>String,Array,Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>node-key</td>
<td>每个树节点用来作为唯一标识的属性，整棵树应该是唯一的</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>entry-title</td>
<td>弹出框的标题</td>
<td>String</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>lazy</td>
<td>是否懒加载</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>load</td>
<td>加载子树数据的方法，仅当 lazy 属性为true 时生效，resolve有两个参数：参数1为子节点数组，参数2为在懒加载后定位的node-key</td>
<td>Boofunction(node, resolve)lean</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁弹出树</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>是否将弹出树设置为只读模式</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>onlyChooseLeaf</td>
<td>是否只能选择末级节点</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Toolbar Events</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-search-change</td>
<td>搜索框输入时触发</td>
<td>当不是懒加载时，参数为搜索框的value；当为懒加载时，共两个参数，依次为：搜索时搜索框的内容、 搜索结果的回调函数resolve，传入搜索的节点数组。</td>
</tr>
<tr>
<td>on-search-select</td>
<td>搜索选择时触发</td>
<td>有一个参数：选中的节点数据</td>
</tr>
<tr>
<td>on-lazy-search</td>
<td>当为懒加载时，弹出树搜索框输入时触发</td>
<td>共两个参数，依次为：搜索时搜索框的内容、 搜索结果的回调函数resolve，传入搜索的节点数组</td>
</tr>
<tr>
<td>on-modal-select</td>
<td>当弹出树选中节点确定后触发</td>
<td>共一个参数：选中的节点node</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data () {
    return {
      treeData: [
        {
          id: 1,
          label: '一级 1',
          hasChildren: true,
          children: [{
            id: 4,
            label: '二级 1-1',
            hasChildren: true,
            children: [{
              id: 9,
              label: '三级 1-1-1',
              hasChildren: false
            }, {
              id: 10,
              label: '三级 1-1-2',
              hasChildren: false
            }]
          }]
        }, {
          id: 2,
          label: '一级 2',
          hasChildren: true,
          children: [{
            id: 5,
            label: '二级 2-1',
            hasChildren: false
          }, {
            id: 6,
            label: '二级 2-2',
            hasChildren: false
          }]
        }
      ],
      treeDataList: [
        {
          id: 1,
          label: '一级 1',
          hasChildren: true,
          nodeNamePaths: '一级 1',
          nodePaths: '1',
          parent: null,
        },
        {
          id: 4,
          label: '二级 1-1',
          nodeNamePaths: '一级 1/二级 1-1',
          nodePaths: '1/4',
          hasChildren: true,
          parent: 1
        },
        {
          id: 9,
          label: '三级 1-1-1',
          hasChildren: false,
          nodeNamePaths: '一级 1/二级 1-1/三级 1-1-1',
          nodePaths: '1/4/9',
          parent: 4
        },
        {
          id: 10,
          label: '三级 1-1-2',
          hasChildren: false,
          nodeNamePaths: '一级 1/二级 1-1/三级 1-1-2',
          nodePaths: '1/4/10',
          parent: 4
        },
        {
          id: 2,
          label: '一级 2',
          hasChildren: true,
          nodeNamePaths: '一级 2',
          nodePaths: '2',
          parent: null,
        },
        {
          id: 5,
          label: '二级 2-1',
          hasChildren: false,
          nodeNamePaths: '一级 2/二级 2-1',
          nodePaths: '2/5',
          parent: 2
        },
        {
          id: 6,
          label: '二级 2-2',
          hasChildren: false,
          nodeNamePaths: '一级 2/二级 2-2',
          nodePaths: '2/6',
          parent: 2
        }
      ],
      modalTreeLazyData: [],
      defaultValue: '',
      defaultValue2: '',
      defaultValue3: []
    }
  },
  methods: {
    loadNodes(node, resolve) {
      if (node.level === 0) {
        // data为异步请求得到的数据,此处为模拟
        const data = this.treeDataList.filter(item => item.parent === null)
        return resolve(data)
      }

      if (node.level > 0) {
        // data为异步请求得到的数据,此处为模拟
        const data = this.treeDataList.filter(item => item.parent === node.data.id)
        // 提供第二个参数，能更好的高亮展开选中节点
        resolve(data, this.currentNodeData.id)
      }
    },
    handleLazySearch (label, resolve) {
      const data = this.treeDataList.filter(item => item.label.includes(label))
      resolve(data)
    }
  }
}
</script>


