<template>
<div class="modal-tree" ref="acctModalTree">
  <div class="select-box">
    <Input
    v-model="searchText"
    :disabled="disabled"
    :readonly="readonly"
    placeholder="请输入关键字..."
    @on-change="handleChanged"
    @on-focus="handleFocus"
    @on-blur="handleBlur"
    @keydown.down.native="handleSelectNext"
    @keydown.up.native="handleSelectPrev"
    @keydown.enter.native="handleSelected"
    >
      <Icon type="ios-more" class="more-select" slot="suffix" @click="handleInputSelect" />
    </Input>
  </div>
  <ul ref="searchList"
  class="main-search-list acct-search-list"
  :class="{
    'is-focus': hasFocus,
    'position-left-bottom': !positionTop && positionLeft,
    'position-left-top': positionTop && positionLeft,
    'position-right-bottom': !positionTop && !positionLeft,
    'position-right-top': positionTop && !positionLeft }"
  v-show="searchNodes.length > 0 && showSearchResult"
  @mouseleave="onResultList = false"
  @mouseenter="onResultList = true">
    <li :class="['acct-search-item', selectIndex === index ? 'selected' : '']"
    v-for="(item, index) in searchNodes" :key="item.id"
    @click="handleMainSearchSelectClick(item)"
    >
      <span class="acct-search-label">{{ item.label }}</span>
      <!-- <span class="acct-search-path">{{ item.nodePath }}</span> -->
    </li>
  </ul>

  <Modal
  class-name="tree-select-modal"
  width="660"
  v-model="entryModalVisible"
  :title="entryTitle"
  @on-visible-change="handleEntryModalVisibleChanged"
  >
    <div class="search-box">
      <Input
      search v-model="entrySearchText"
      placeholder="请输入关键字进行搜索..."
      @on-change="handleEntrySearchChanged"
      ></Input>
    </div>
    <div class="filter-box">
      <div>
        <Checkbox v-model="showCommonEntries">仅显示常用条目</Checkbox>
        <Checkbox v-model="showMatchesChildren">显示匹配项下级</Checkbox>
      </div>
      <Button size="small" @click="handleCommonSetting">常用条目设置</Button>
    </div>
    <div :class="{'tree-box': true, 'tree-entrySearching': entrySearching, 'tree-common-only': showCommonEntries}">
      <base-tree
      ref="tree"
      :data="baseData"
      :node-key="nodeKey"
      :lazy="lazy"
      :load="load"
      :show-buttons="false"
      :filter-node-method="filterNode"
      @node-click="handleNodeClick"
      ></base-tree>
    </div>
    <div :class="{'common-tree-box': true, 'tree-common-only': showCommonEntries}">
      <base-tree
      ref="commonTree"
      :data="commonData"
      :node-key="nodeKey"
      :show-buttons="false"
      :default-expand-all="true"
      :filter-node-method="filterCommonNode"
      @node-click="handleNodeClick"
      ></base-tree>
    </div>
    <div :class="{'search-tree-box': true, 'tree-entrySearching': entrySearching && !showCommonEntries}">
      <ul ref="entrySearchList" class="acct-search-list">
        <li class="acct-search-item"
        v-for="(item, index) in entrySearchResult" :key="item[nodeKey]"
        @click="handleSelectClick(item)"
        >
          <span class="acct-search-label">{{ item.label }}</span>
          <span class="acct-search-path">{{ item.nodePath }}</span>
        </li>
      </ul>
    </div>
    <div slot="footer">
      <Button type="text" @click="entryModalVisible = false">取消</Button>
      <Button type="primary" @click="handleOkClicked">确定</Button>
    </div>
  </Modal>

  <common-setting
  ref="commonSetting"
  :data="allEntriesData"
  :lazy="lazy"
  :load="load"
  :node-key="nodeKey"
  @on-transferred="data => commonData = data"
  @on-left-search="handleLeftSearch"
  @on-left-checked="(key, resolve) => $emit('on-left-checked', key, resolve)"
  ></common-setting>
</div>
</template>

<script>
import BaseTree from '../base-tree'
import CommonSetting from '../transfer-tree'
export default {
  name: 'ModalTree',
  components: {
    BaseTree,
    CommonSetting
  },
  model: {
    event: 'selected'
  },
  props: {
    entryTitle: {
      type: String,
      required: true
    },
    value: {
      type: [String, Array, Number]
    },
    data: {
      type: Array,
      required: true,
      default () {
        return []
      }
    },
    nodeKey: String,
    lazy: {
      type: Boolean,
      default: false
    },
    load: Function,
    onlyChooseLeaf: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      hasFocus: false,
      positionTop: false,
      positionLeft: true,

      searchText: '', // 搜索框的搜索文本
      searchNodes: [], // 搜索结果列表的渲染数据
      showSearchResult: false, // 是否显示搜索结果
      selectIndex: -1, // 当前选中的搜索结果索引
      selectedItem: {}, // 选中的搜索结果对象
      onResultList: false, // 判断鼠标是否停留在搜索结果上

      entrySearchText: '',
      entryModalVisible: false,
      showCommonEntries: false, // 仅显示常用条目
      showMatchesChildren: false, // 显示匹配项下级
      currentEntryNodeData: {},
      entrySearchResult: [],
      entrySearching: false,
      baseData: [],
      allEntriesData: [],
      commonEntriesData: [],
      commonData: [],
      queryData: [], // 用来查询这个树上的某个节点的数组
    }
  },
  methods: {
    // 最外层输入框的搜索功能
    handleChanged () {
      this.searchNodes = []
      if (this.searchText === '') {
        this.hasFocus = false
        return;
      }
      this.showSearchResult = true;
      this.hasFocus = true
      this.setTreePosition()
      if (!this.lazy) {
        this.currentEntryNodeData = {}
        this.$emit('on-search-change', this.searchText)
        this.filterTreeToList(this.data, this.searchText)
        return
      }
      this.currentEntryNodeData = {}
      this.$refs.tree.expandNodes([])
      this.$emit('on-search-change', this.searchText, this.resolveMainResult);
    },

    filterTreeToList (data, text) {
      data.filter(item => {
        if (item.label.includes(text)) {
          this.searchNodes.push(item)
        }
        if (item.children) {
          this.filterTreeToList(item.children, text)
        }
      })
    },

    findItemInTree (data, text) {
      console.log(this.nodeKey)
      for (let i = 0; i < data.length; i++) {
        let item = data[i]
        if (item[this.nodeKey] === text) {
          return item
        }
        if (item.children) {
          const result = this.findItemInTree(item.children, text)
          if (result) {
            return result
          }
        }
      }
    },

    handleFocus () {
      this.hasFocus = this.searchText === '' ? false : true
      // this.showSearchResult = true;
      this.$emit('on-search-focus');
    },

    handleBlur () {
      if (!this.onResultList) {
        this.hasFocus = false
        this.showSearchResult = false
      }

      this.$emit('on-search-blur');
    },

    handleSelectPrev () {
      if (this.selectIndex > 0) {
        this.selectIndex--;
        this.selectedItem = this.searchNodes[this.selectIndex];
        this.searchText = this.selectedItem.label;
      }
    },

    handleSelectNext () {
      if (this.selectIndex < this.searchNodes.length - 1) {
        this.selectIndex++;
        this.selectedItem = this.searchNodes[this.selectIndex];
        this.searchText = this.selectedItem.label;
      }
    },

    handleSelected (e) {
      if (e) {
        e.target.blur();
      }
      this.selectIndex = -1;
      this.showSearchResult = false;
      this.$emit('on-search-selected', this.selectedItem);
      this.$emit('selected', this.selectedItem[this.nodeKey])
    },

    handleMainSearchSelectClick (item) {
      console.log(item.label)
      this.searchText = item.label
      this.selectedItem = item
      this.currentEntryNodeData = item
      this.handleSelected()
    },

    handleSelectClick (item) {
      this.entrySearchText = item.label
      this.selectedItem = item
      this.currentEntryNodeData = item
      this.handleSelected();
    },

    setTreePosition () {
      const rect = this.$refs.acctModalTree.getBoundingClientRect()
      console.log()
      // const parentRect = this.$refs.acctSelectTree.parentNode.getBoundingClientRect()
      if (this.$refs.searchList) {
        const height = this.$refs.searchList.offsetHeight
        const width = this.$refs.searchList.offsetWidth
        if (window.innerHeight - rect.bottom < height + 10 && width <= window.innerWidth - rect.left + 20) { // 左下
          this.positionTop = false
          this.positionLeft = true
        } else if (window.innerHeight - rect.bottom >= height + 10 && width <= window.innerWidth - rect.left + 20) { // 左上
          this.positionTop = true
          this.positionLeft = true
        } else if (window.innerHeight - rect.bottom >= height + 10 && width > window.innerWidth - rect.left + 20) { // 右上
          this.positionTop = true
          this.positionLeft = false
        } else if (window.innerHeight - rect.bottom < height + 10 && width > window.innerWidth - rect.left + 20) { // 右下
          this.positionTop = false
          this.positionLeft = true
        }
      }
    },

    filterNode (value, data) {
      if (!value) return true
      if (this.showCommonEntries) return true
      if (this.lazy) {
        return data.label === value
      } else {
        return data.label.indexOf(value) !== -1
      }
    },
    filterCommonNode (value, data) {
      if (!value) return true
      if (this.showCommonEntries) {
        return data.label.indexOf(value) !== -1
      }
    },
    handleInputSelect () {
      if (this.disabled || this.readonly) return
      this.entryModalVisible = true
      this.$emit('on-select')
    },
    handleEntrySearchChanged () {
      if (this.showCommonEntries) {
        return this.$refs.commonTree.filter(this.entrySearchText)
      }
      if (this.lazy) {
        this.currentEntryNodeData = {}
        if (this.entrySearchText === '') {
          this.entrySearching = false
        } else {
          this.entrySearching = true
          this.$emit('on-lazy-search', this.entrySearchText, this.resolveResult)
        }
      } else {
        this.$refs.tree.filter(this.entrySearchText)
      }
    },
    handleNodeClick (nodeData, node) {
      this.currentEntryNodeData = nodeData
    },
    handleEntryModalVisibleChanged () {
      this.baseData = JSON.parse(JSON.stringify(this.data))

      // 默认展开一级节点
      let defaultExpands = []
      this.baseData.forEach(item => {
        defaultExpands.push(item[this.nodeKey], true)
      })
      this.$refs.tree.expandNodes(defaultExpands)

      if (!this.lazy) {
        this.$nextTick(() => {
          if (!this.currentEntryNodeData) return
          const node = this.$refs.tree.getNode(this.currentEntryNodeData[this.nodeKey])
          if (!node) return
          let parentList = []
          this.getParentList(node, parentList)
          this.$refs.tree.expandNodes(parentList)
        })

      } else {
        if (this.currentEntryNodeData.nodePaths)
          this.$refs.tree.expandNodes(this.currentEntryNodeData.nodePaths.split('/'))
      }

      this.entrySearchText = ''
      this.entrySearching = false
    },
    getParentList (node, list) {
      list.unshift(node.data[this.nodeKey])
      if (node.level > 1) {
        return this.getParentList(node.parent, list)
      }

      return list
    },
    handleOkClicked () {
      if (!this.currentEntryNodeData) {
        this.$Modal.info({
          title: '提示',
          content: '请选择一个项目'
        })
        return
      }
      if (this.currentEntryNodeData.hasChildren && this.onlyChooseLeaf) {
        this.$Modal.info({
          title: '提示',
          content: '当前选择的节点不是末级节点'
        })
        return
      }
      this.searchText = this.currentEntryNodeData.label
      this.$emit('selected', this.currentEntryNodeData[this.nodeKey])
      this.entryModalVisible = false
    },
    handleCommonSetting () {
      this.allEntriesData = JSON.parse(JSON.stringify(this.data))
      this.$refs.commonSetting.setModalVisible()
    },
    // handleShowCommonOnly (only) {
    //   if (only) {
    //   }
    // },
    handleLeftSearch (entrySearchText, resolve) {
      this.$emit('on-left-search', entrySearchText, resolve)
    },

    resolveMainResult (data) {
      if (Array.isArray(data)) {
        data.forEach(item => {
          item.hasChildren = false
          item.icon = 'file'
        })
        this.searchNodes = data
      }
    },

    resolveResult (data) {
      if (Array.isArray(data)) {
        data.forEach(item => {
          item.hasChildren = false
          item.icon = 'file'
        })
        this.entrySearchResult = data
      }
    },

    setLabel (value) {
      this.$nextTick(() => {
        const node = this.findItemInTree(this.queryData, value)
        if (node)  {
          this.currentEntryNodeData = node
          this.searchText = node.label
        }
        else this.searchText = ''
      })
    }
  },
  mounted () {
    this.queryData = this.data
    this.setLabel(this.value)
  },
  watch: {
    value (v) {
      this.setLabel(v)
    },
    data (d) {
      this.queryData = d
      this.setLabel(this.value)
    }
  }
}
</script>

<style>
.modal-tree {
  height: 32px;
  background-color: #fff;
  position: relative;
}

.modal-tree .more-select {
  font-size: 26px;
}

.modal-tree .main-search-list {
  position: absolute;
  z-index: 999;
  min-height: 42px;
  max-height: 400px;
  min-width: 100%;
  overflow: auto;
  padding: 5px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 6px rgba(0,0,0,.2);
  display: none;
}
.modal-tree .main-search-list.is-focus {
  display: block;
}

.modal-tree .main-search-list.position-left-bottom {
  left: 0;
  bottom: calc(100% + 2px);
}

.modal-tree .main-search-list.position-left-top {
  left: 0;
  top: calc(100% + 2px);
}

.modal-tree .main-search-list.position-right-bottom {
  right: 0;
  bottom: calc(100% + 2px);
}

.modal-tree .main-search-list.position-right-top {
  right: 0;
  top: calc(100% + 2px);
}

.modal-tree .acct-search-list {
  position: absolute;
  /* top: 44px; */
  /* left: 10px; */
  z-index: 999;
  /* min-width: calc(100% - 20px); */
  /* max-height: calc(100% - 52px); */
  /* overflow-y: auto; */
  margin: 0;
  padding: 6px 0;
  /* border: 1px solid #ddd; */
  /* border-radius: 4px; */
  box-sizing: border-box;
  background-color: #fff;
  list-style: none;
}

.modal-tree .acct-search-list .acct-search-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 7px 7px;
  box-sizing: border-box;
  line-height: 16px;
}

.modal-tree .acct-search-list .acct-search-item.selected {
  background-color: #F4F9FF;
}

.modal-tree .acct-search-list .acct-search-item .acct-search-label {
  /* width: 200px; */
  margin-right: 20px;
  white-space: nowrap;
}

.modal-tree .acct-search-list .acct-search-item .acct-search-path {
  white-space: nowrap;
  color: #aaa;
}

.modal-tree .acct-search-list .acct-search-item:hover {
  background-color: #F4F9FF;
  cursor: pointer;
}

.tree-select-modal .search-box {
  margin-bottom: 10px;
}

.tree-select-modal .acct-tree-body {
  height: 100%;
}

.tree-select-modal .filter-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 32px;
  padding: 0 16px;
  margin-bottom: 10px;
  background-color: #F8F8F8;
}

.tree-select-modal .filter-box button {
  padding: 4px 12px;
}

.tree-select-modal .filter-box button > span {
  display: block;
  height: 14px;
  font-size: 12px;
  line-height: 14px;
}


.tree-select-modal .tree-box {
  height: 375px;
}
.tree-select-modal .tree-box.tree-entrySearching {
  display: none;
}

.tree-select-modal .search-tree-box.tree-entrySearching {
  display: block;
}

.tree-select-modal .search-tree-box {
  display: none;
  height: 375px;
  overflow: auto;
}

.tree-select-modal .common-tree-box {
  display: none;
  height: 375px;
  overflow: auto;
}

.tree-select-modal .tree-box.tree-common-only {
  display: none;
}

.tree-select-modal .common-tree-box.tree-common-only {
  display: block;
}

.tree-select-modal .ivu-icon-ios-information-circle {
  font-size: 28px;
}

.tree-select-modal .search-tree-box .acct-search-list {
  padding: 6px 0;
  box-sizing: border-box;
  background-color: #fff;
  list-style: none;
}

.tree-select-modal .search-tree-box .acct-search-list .acct-search-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 7px 10px;
  box-sizing: border-box;
  line-height: 16px;
}

.tree-select-modal .search-tree-box .acct-search-list .acct-search-item.selected {
  background-color: #F4F9FF;
}

.tree-select-modal .search-tree-box .acct-search-list .acct-search-item .acct-search-label {
  margin-right: 20px;
}

.tree-select-modal .search-tree-box .acct-search-list .acct-search-item .acct-search-path {
  color: #aaa;
}

.tree-select-modal .search-tree-box .acct-search-list .acct-search-item:hover {
  background-color: #F4F9FF;
  cursor: pointer;
}
</style>



