</div></demo-block>
<h2>手动禁用启用</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div&gt;\n    &lt;checkbox @on-change=\&quot;handleCheckboxChanged\&quot;&gt;是否禁用&lt;/checkbox&gt;\n    &lt;toolbar ref=\&quot;toolbar\&quot; :buttons=\&quot;toolbarButtons\&quot;&gt;&lt;/toolbar&gt;\n  &lt;/div&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      toolbarButtons: [\n        { key: 'create-group', name: '创建分组' },\n        { key: 'update-group', name: '修改分组' },\n        { key: 'delete-group', name: '删除分组' },\n        { key: 'create-define', name: '创建定义' },\n        { key: 'update-define', name: '修改定义' },\n        { key: 'delete-define', name: '删除定义' },\n        { key: 'execute', name: '执行' },\n        { key: 'moveup', name: '上移' },\n        { key: 'movedown', name: '下移' },\n        { key: 'import', name: '导入' },\n        { key: 'export', name: '导出' },\n        { key: 'refresh', name: '刷新' }\n      ]\n    }\n  },\n  methods: {\n    handleCheckboxChanged (check) {\n      if (check) {\n        this.$refs.toolbar.disableButtons(['delete-group', 'update-define', 'import'])\n      } else {\n        this.$refs.toolbar.enableButtons(['delete-group', 'update-define', 'import'])\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <div>
    <checkbox @on-change="handleCheckboxChanged">是否禁用</checkbox>
    <toolbar ref="toolbar" :buttons="toolbarButtons"></toolbar>
  </div>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">@on-change</span>=<span class="hljs-string">"handleCheckboxChanged"</span>&gt;</span>是否禁用<span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">toolbar</span>
    <span class="hljs-attr">ref</span>=<span class="hljs-string">"toolbar"</span>
    <span class="hljs-attr">:buttons</span>=<span class="hljs-string">"toolbarButtons"</span>
    &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">toolbar</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">toolbarButtons</span>: [
        { <span class="hljs-attr">key</span>: <span class="hljs-string">'create-group'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'创建分组'</span> },
        { <span class="hljs-attr">key</span>: <span class="hljs-string">'update-group'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'修改分组'</span> },
        { <span class="hljs-attr">key</span>: <span class="hljs-string">'delete-group'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'删除分组'</span> },
        { <span class="hljs-attr">key</span>: <span class="hljs-string">'create-define'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'创建定义'</span> },
        { <span class="hljs-attr">key</span>: <span class="hljs-string">'update-define'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'修改定义'</span> },
        { <span class="hljs-attr">key</span>: <span class="hljs-string">'delete-define'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'删除定义'</span> },
        { <span class="hljs-attr">key</span>: <span class="hljs-string">'execute'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'执行'</span> },
        { <span class="hljs-attr">key</span>: <span class="hljs-string">'moveup'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'上移'</span> },
        { <span class="hljs-attr">key</span>: <span class="hljs-string">'movedown'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'下移'</span> },
