  },
  <span class="hljs-attr">methods</span>: {
    handleCheckboxChanged (check) {
      <span class="hljs-keyword">if</span> (check) {
        <span class="hljs-keyword">this</span>.$refs.toolbar.disableButtons([<span class="hljs-string">'delete-group'</span>, <span class="hljs-string">'update-define'</span>, <span class="hljs-string">'import'</span>])
      } <span class="hljs-keyword">else</span> {
        <span class="hljs-keyword">this</span>.$refs.toolbar.enableButtons([<span class="hljs-string">'delete-group'</span>, <span class="hljs-string">'update-define'</span>, <span class="hljs-string">'import'</span>])
