</div></demo-block>
<h2>API</h2>
<h3>Toolbar props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>buttons</td>
<td>用来渲染toolbar的数组，其对象包含 key 和 name 属性</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>disables</td>
<td>默认要禁用的按钮数组，由按钮对象的 key 值组成</td>
<td>Array</td>
<td>[]</td>
</tr>
</tbody>
</table>
<h3>Toolbar events</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-[key]</td>
<td>toolbar中按钮的事件生成规则：在传入的 key 值前面加上 on ，如on-create-group</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>Toolbar methods</h3>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>checkDisabled</td>
<td>检测某个toolbar按钮是否被禁用，禁用返回true，否则返回false</td>
<td>该按钮的 key 值</td>
</tr>
<tr>
<td>disableButtons</td>
<td>手动禁用一个或多个toolbar按钮</td>
<td>按钮的 key 值组成的数组</td>
</tr>
<tr>
<td>enableButtons</td>
<td>手动启用一个或多个toolbar按钮</td>
<td>按钮的 key 值组成的数组</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data () {
    return {
      toolbarButtons: [
        { key: 'create-group', name: '创建分组' },
        { key: 'update-group', name: '修改分组' },
        { key: 'delete-group', name: '删除分组' },
        { key: 'create-define', name: '创建定义' },
        { key: 'update-define', name: '修改定义' },
        { key: 'delete-define', name: '删除定义' },
        { key: 'execute', name: '执行' },
        { key: 'moveup', name: '上移' },
        { key: 'movedown', name: '下移' },
        { key: 'import', name: '导入' },
        { key: 'export', name: '导出' },
        { key: 'refresh', name: '刷新' }
      ],
      disables: ['delete-group', 'update-define', 'import']
    }
  },
  methods: {
    handleCheckboxChanged (check) {
      if (check) {
        this.$refs.toolbar.disableButtons(['delete-group', 'update-define', 'import'])
      } else {
        this.$refs.toolbar.enableButtons(['delete-group', 'update-define', 'import'])
      }
    }
  }
}
</script>


