<template>
<div class="acct-select-tree" :class="{ 'is-disabled': disabled }" ref="acctSelectTree">
  <div class="acct-select" ref="selectBox">
    <Input
    class="select-tree-input"
    ref="selectInput"
    v-model="searchText"
    :readonly="multiple || !searchable"
    :clearable="singleClearable"
    @click.native="hasFocus = !hasFocus"
    @on-blur="hasFocus = false"
    @on-change="handleChanged"
    @on-clear="handleClear"
    placeholder="请选择..."
    />
    <div :class="{'select-tags': true, 'show-tags': multiple}" ref="selectTags" @click="handleSelectTagsClick">
      <Tag type="border"
      closable
      v-for="tag in selectTags"
      :name="tag[nodeKey]"
      :key="tag[nodeKey]"
      @on-close="handleTagClosed"
      @click.native="handleSelectTagsClick"
      >{{tag.label}}</Tag>
    </div>
    <Icon type="ios-arrow-down" :class="{'is-focus': hasFocus}" />
    <div class="disable-mask"></div>
  </div>
  <div ref="treeBox"
  :class="{
    'acct-tree-box': true,
    'is-focus': hasFocus,
    'position-left-bottom': !positionTop && positionLeft,
    'position-left-top': positionTop && positionLeft,
    'position-right-bottom': !positionTop && !positionLeft,
    'position-right-top': positionTop && !positionLeft }"
  >
    <base-tree
    ref="selectTree"
    :data="options"
    :node-key="nodeKey"
    :show-icon="false"
    :filter-node-method="filterNode"
    :expand-on-click-node="false"
    @node-click="handleNodeClicked"
    @node-expand="setTreePosition()"
    ></base-tree>
  </div>
</div>
</template>

<script>
import BaseTree from '../base-tree'
export default {
  name: 'SelectTree',
  components: {
    BaseTree
  },
  model: {
    event: 'selected'
  },
  props: {
    options: {
      type: Array,
      required: true
    },
    value: {
      type: [String, Array, Number]
    },
    nodeKey: {
      type: String,
      required: true
    },
    clearable: Boolean,
    multiple: {
      type: Boolean,
      default: false
    },
    searchable: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: '请输入关键字查询...'
    }
  },
  data () {
    return {
      selectTreeDom: null,
      treeNodesDom: null,
      hasFocus: false,
      singleClearable: false,
      searchText: '',
      selectTags: [],
      positionTop: false,
      positionLeft: true,
    }
  },
  methods: {
    filterNode (value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    handleChanged () {
      this.$refs.selectTree.filter(this.searchText)

    },
    handleNodeClicked (data) {
      if (this.multiple) {
        if (this.selectTags.findIndex(item => item[this.nodeKey] === data[this.nodeKey]) === -1) {
          this.selectTags.push(data)
        } else {
          this.selectTags = this.selectTags.filter(item => item[this.nodeKey] !== data[this.nodeKey])
        }
        const node = this.$refs.selectTree.getNode(data[this.nodeKey])
        node.data.selectedInTree = !node.data.selectedInTree
        const select = []
        this.selectTags.forEach(item => {
          select.push(item[this.nodeKey])
        })
        this.$emit('selected', select)
        this.$emit('select-changed', this.selectTags, data)
      } else {
        this.searchText = data.label
        this.$emit('selected', data[this.nodeKey])
        this.hasFocus = false
        this.$refs.selectInput.blur()
        this.$emit('select-changed', data)
      }
    },

    handleMouseDown (e) {
      if (e.target.className.includes('tree') || e.target.className.includes('ivu-icon')
      || e.target.className.includes('tag')) {
        e.preventDefault()
      }
    },

    handleTagClosed (e, key) {
      this.selectTags = this.selectTags.filter(item => item[this.nodeKey] !== key)
      const node = this.$refs.selectTree.getNode(key)
      node.data.selectedInTree = false
      this.$refs.selectTree.updateNode(node, node.data)
      this.$emit('select-changed', this.selectTags, node.data)
    },

    handleSelectTagsClick () {
      this.$refs.selectInput.focus()
      this.hasFocus = !this.hasFocus
    },

    handleClear () {
      // this.$refs.selectTree.setCurrentKey(0)
    },

    expandTree (node) {
      node.expanded = true
      this.setTreePosition()
      if (node.parent) {
        this.expandTree(node.parent)
      }
    },

    setTreePosition () {
      const rect = this.$refs.acctSelectTree.getBoundingClientRect()
      // const parentRect = this.$refs.acctSelectTree.parentNode.getBoundingClientRect()
      if (this.$refs.treeBox) {
        const height = this.$refs.treeBox.offsetHeight
        const width = this.$refs.treeBox.offsetWidth
        if (window.innerHeight - rect.bottom < height + 10 && width <= window.innerWidth - rect.left + 20) { // 左下
          this.positionTop = false
          this.positionLeft = true
        } else if (window.innerHeight - rect.bottom >= height + 10 && width <= window.innerWidth - rect.left + 20) { // 左上
          this.positionTop = true
          this.positionLeft = true
        } else if (window.innerHeight - rect.bottom >= height + 10 && width > window.innerWidth - rect.left + 20) { // 右上
          this.positionTop = true
          this.positionLeft = false
        } else if (window.innerHeight - rect.bottom < height + 10 && width > window.innerWidth - rect.left + 20) { // 右下
          this.positionTop = false
          this.positionLeft = true
        }
      }
    },

    initTree () {
      if (this.value) {
        setTimeout(() => {
          if (this.multiple) {
            this.value.forEach(item => {
              const node = this.$refs.selectTree.getNode(item)
              if (!node) return
              node.data.selectedInTree = true
              this.selectTags.push(node.data)
              this.expandTree(node)
            })
          } else {

              const node = this.$refs.selectTree.getNode(this.value)
              if (!node) return
              this.$refs.selectTree.setCurrentKey(this.value)
              this.searchText = node.data.label
              this.expandTree(node)
          }
        }, 500);
      }
    }
  },
  created () {
    this.singleClearable = this.clearable
    if (this.multiple) {
      this.singleClearable = false
    }
  },
  mounted () {
    this.selectTreeDom = document.querySelector('.acct-select-tree')
    this.selectTreeDom.addEventListener('mousedown', this.handleMouseDown, false)

    this.initTree()
    this.setTreePosition()
  },

  watch: {
    value () {
      if (!this.multiple) {
        this.initTree()
      }
    },
    hasFocus (focus) {
      focus ? this.$emit('on-open') : this.$emit('on-close')
    },
    searchText (value) {
      if (value === '') {
        this.$emit('selected', '')
      } else {
        if (!this.$refs.selectTree.getNode(this.value)) {
          this.$emit('selected', '')
        }
      }
    }
  },
  updated () {
    this.setTreePosition()
  },
  beforeDestroy () {
    if (this.selectTreeDom) {
      this.selectTreeDom.removeEventListener('mousedown', this.handleMouseDown, false)
    }
  }

}
</script>

<style>
.acct-select-tree {
  position: relative;
  background-color: #fff;
  /* padding: 10px; */
}

.acct-select-tree.is-disabled {
  background-color: #F3F3F3;
}

.acct-select-tree.is-disabled .select-tags {
  background-color: #F3F3F3;
}

.acct-select-tree.is-disabled .select-tags .ivu-tag {
  background-color: #F3F3F3 !important;
}

.acct-select-tree .acct-select {
  position: relative;
  min-height: 32px;
  padding: 2px 0 2px 6px;
}

.acct-select-tree .acct-select .select-tree-input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  height: 100%;
  /* background-color: transparent; */
}

.acct-select-tree .acct-select .ivu-input {
  padding-right: 32px;
  background-color: transparent;
  height: 100%;
}

.acct-select-tree .acct-select > .ivu-icon {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 8;
  width: 32px;
  height: 32px;
  line-height: 32px;
  text-align: center;
}

.acct-select-tree .acct-select .ivu-icon-ios-close-circle {
  z-index: 10;
}

.acct-select-tree .acct-select .ivu-icon.is-focus {
  transform: rotate(-180deg);
}

.acct-select-tree.is-disabled .acct-select .ivu-icon-ios-arrow-down {
  color: #DDDDDD;
}

.acct-select-tree .select-tags {
  display: none;
  position: relative;
  z-index: 10;
  min-height: 26px;
  width: calc(100% - 38px);
  border-radius: 4px;
  background-color: #fff;
  overflow: hidden;
}
.acct-select-tree .select-tags.show-tags {
  display: block;
}
.acct-select-tree .select-tags .ivu-tag {
  float: left;
  max-width: 100%;
  line-height: 22px;
}
.acct-select-tree .select-tags .ivu-tag .ivu-tag-text {
  display: inline-block;
  vertical-align: middle;
  line-height: 18px;
  padding-right: 28px;
  word-break: break-all;
}
.acct-select-tree .select-tags .ivu-tag.ivu-tag-border {
  min-height: 24px;
  padding-right: 0;
}

.acct-select-tree .select-tags .ivu-tag.ivu-tag-border .ivu-icon-ios-close {
  margin-left: 4px !important;
  position: absolute;
  left: calc(100% - 22px);
  top: 4px;
}

.acct-select-tree .acct-tree-box {
  position: absolute;
  z-index: 999;
  min-height: 42px;
  max-height: 400px;
  min-width: 100%;
  overflow: auto;
  padding: 5px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 6px rgba(0,0,0,.2);
  display: none;
}
.acct-select-tree .acct-tree-box.is-focus {
  display: block;
}

.acct-select-tree .acct-tree-box.position-left-bottom {
  left: 0;
  bottom: calc(100% + 4px);
}

.acct-select-tree .acct-tree-box.position-left-top {
  left: 0;
  top: calc(100% + 4px);
}

.acct-select-tree .acct-tree-box.position-right-bottom {
  right: 0;
  bottom: calc(100% + 4px);
}

.acct-select-tree .acct-tree-box.position-right-top {
  right: 0;
  top: calc(100% + 4px);
}

.acct-select-tree .acct-tree {
  border-radius: 4px;
}

.acct-select-tree .acct-tree-body > .acct-tree-node .acct-tree-node__content {
  padding-right: 32px;
  position: relative;
}

.acct-select-tree .acct-tree-body > .acct-tree-node .acct-select-icon {
  position: absolute;
  right: 9px;
  z-index: 100;
}

.acct-select-tree .acct-tree-body .acct-tree-node > .acct-tree-node__content {
  color: #515a6e;
}

.acct-select-tree .acct-tree-body .acct-tree-node.is-selected > .acct-tree-node__content {
  color: #4880ff;
}

.acct-select-tree .acct-select .disable-mask {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;
  width: 100%;
  height: 100%;
  border-radius: 4px;
  background-color: transparent;
  cursor: not-allowed;
}

.acct-select-tree.is-disabled .acct-select .disable-mask {
  display: block;
}

</style>



