import Install from './docs/introduce.md'
import Toolbar from './docs/toolbar.md'
import BaseTree from './docs/base-tree.md'
import ModalTree from './docs/modal-tree.md'
import SelectTree from './docs/select-tree.md'
const routes = [
  {
    path: '/',
    component: Install,
    name: 'default'
  },
  {
    path: '/introduce',
    name: 'introduce',
    component: Install
  },
  {
    path: '/toolbar',
    name: 'toolbar',
    component: Toolbar
  },
  {
    path: '/basetree',
    name: 'basetree',
    component: BaseTree
  },
  {
    path: '/modaltree',
    name: 'modaltree',
    component: ModalTree
  },
  {
    path: '/selecttree',
    name: 'selecttree',
    component: SelectTree
  }
]

export default routes



